<template>
  <div class="container">
    <p class="movie-caption movie-caption-top250">Top250</p>
    <div class="movie-rank-list" v-show='showList'>
      <ul>
        <li v-for='movie,index in movies' :key='index'>
          <p class="movie-rank-rank">{{index + firstRank}}</p>
          <router-link :to="{path:'movieDetail' , query:{id: movie.id}}" class="movie-rank-cover"><img :src="'https://images.weserv.nl/?url=' + movie.images.small"></router-link>
          <div class="movie-rank-inf">
            <router-link :to="{path:'movieDetail' , query:{id: movie.id}}" class="movie-rank-title">{{movie.title}}</router-link>
            <p class="movie-rank-pubdate">上映日期：{{arrayTransform(movie.pubdates)}}</p>
            <p class="movie-rank-genres">类型：{{arrayTransform(movie.genres)}}</p>
            <div class="movie-rank-casts">
              <span>导演：</span>
              <span class="movie-casts-name" v-for='director,idx in movie.directors' :key='idx' v-if='idx == 0'>
                <router-link :to="{path: 'artistDetail' , query: {id: director.id}}">{{director.name}}</router-link>
              </span>
              <span class="movie-casts-name" v-else>
                &nbsp;,&nbsp;<router-link :to="{path: 'artistDetail' , query: {id: director.id}}">{{director.name}}</router-link>
              </span>
            </div>
            <div class="movie-rank-casts">
              <span>主演：</span>
              <span class="movie-casts-name" v-for='actor,id in movie.casts' :key='id' v-if='id == 0'>
                <router-link :to="{path: 'artistDetail' , query: {id: actor.id}}">{{actor.name}}</router-link>
              </span>
              <span class="movie-casts-name" v-else>
                &nbsp;,&nbsp;<router-link :to="{path: 'artistDetail' , query: {id: actor.id}}">{{actor.name}}</router-link>
              </span>
            </div>
            <div class="movie-rank-score">
              <movie-score :score='movie.rating.average'></movie-score>
              <p class="movie-rank-people">({{movie.collect_count}}评)</p>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <p class="movie-list-loading" v-show='!showList'>加载中...</p>
    <div class="pages-bgc" v-show='showPages'>
      <div class="pages">
        <a href="#" @click.prevent='changeCurrentPage(-1)'>上一页</a>
        <div class="pages-btn" v-show='pagesStatus == 1'>
          <a href="#" v-for='n,index in maxPage' :key='index' @click.prevent='setCurrentPage(n)' :class="{'current-page' : pageFlag[index]}">{{n}}</a>
        </div>
        <div class="pages-btn" v-show='pagesStatus == 2'>
          <a href="#" v-for='n,index in 8' :key='index' @click.prevent='setCurrentPage(n)' :class="{'current-page' : pageFlag[index]}">{{n}}</a>
          <span>...</span>
          <a href="#" @click.prevent='setCurrentPage(maxPage)' :class="{'current-page' : pageFlag[9]}">{{maxPage}}</a>
        </div>
        <div class="pages-btn" v-show='pagesStatus == 3'>
          <a href="#" @click.prevent='setCurrentPage(1)' :class="{'current-page' : pageFlag[1]}">1</a>
          <span>...</span>
          <a href="#" v-for='n,index in 7' :key='index' @click.prevent='setCurrentPage(currentPage - 4 + n)' :class="{'current-page' : pageFlag[index + 1]}">{{currentPage - 4 + n}}</a>
          <span>...</span>
          <a href="#" @click.prevent='setCurrentPage(maxPage)' :class="{'current-page' : pageFlag[9]}">{{maxPage}}</a>
        </div>
        <div class="pages-btn" v-show='pagesStatus == 4'>
          <a href="#" @click.prevent='setCurrentPage(1)' :class="{'current-page' : pageFlag[1]}">1</a>
          <span>...</span>
          <a href="#" v-for='n,index in 8' :key='index' @click.prevent='setCurrentPage(maxPage - 8 + n)' :class="{'current-page' : pageFlag[index + 1]}">{{maxPage - 8 + n}}</a>
        </div>
        <a href="#" @click.prevent='changeCurrentPage(1)'>下一页</a>
      </div>
    </div>
  </div>
</template>
<style>
  .movie-caption-top250{
    margin-bottom: 0px;
  }
  .movie-rank-list{
    padding: 0 20px;
  }
  .movie-rank-list li{
    display: flex;
    padding-bottom: 10px;
    padding-top: 10px;
    border-bottom: 1px solid #ccc;
    align-items: center;
  }
  .movie-rank-rank{
    font-size: 40px;
    text-align: center;
    padding: 0 20px;
    height: 100px;
    line-height: 100px;
    width: 90px;
  }
  .movie-rank-cover{
    display: block;
    margin-top: 3px;
  }
  .movie-rank-cover img{
    border: 1px solid #ccc;
    width: 84px;
    height: 120px;
    display: block;
  }
  .movie-rank-inf{
    width: 750px;
    margin-left: 40px;
    color: #000;
  }
  .movie-rank-inf a:hover{
    text-decoration: underline;
  }
  .movie-rank-title{
    font-size: 18px;
    color: #000;
  }
  .movie-rank-casts a{
    color: #000;
  }
  .movie-rank-casts{
    display: flex;
  }
  .movie-rank-casts span{
    display: block;
  }
  .movie-rank-score{
    display: flex;
    margin-top: 2px;
  }
  .movie-rank-people{
    font-size: 14px;
    margin-left: 5px;
    color: #666;
  }
  .movie-list-loading{
    font-size: 18px;
    margin-top: 20px;
    text-align: center;
  }
  /*切换页面按钮部分*/
  .pages-bgc{
    display: flex;
    justify-content: center;
    width: 100%;
    margin-bottom: 67px;
    margin-top: 20px;
  }
  .pages{
    display: flex;
  }
  .pages a{
    height: 22px;
    padding: 0 8px;
    line-height: 22px;
    display: inline-block;
    margin: 0 1px 0 2px;
    border: 1px solid #000;
    font-size: 12px;
    color: #000;
  }
  .pages a:hover{
    border-color: #072;
    color: #072;
  }
  .pages span{
    color: #000;
    font-family: Arial, Helvetica, sans-serif;
  }
  .pages-btn{
    display: flex;
  }
  .pages .current-page,.pages .current-page:hover{
    background-color: #072;
    color: #fff;
  }
</style>
<script>
  export default{
    data(){
      return{
        movies: [],
        pageItems: 20,
        total: 250,
        showList: false,
        currentPage: 1,
        pageFlag: [true,false,false,false,false,false,false,false,false],
        showPages: false
      }
    },
    computed: {
      firstRank: function(){
        return (this.currentPage - 1) * this.pageItems + 1
      },
      maxPage: function(){
        return Math.ceil(this.total / this.pageItems)
      },
      pagesStatus: function(){
        if(this.maxPage <= 9) return 1;
        //优先判断状态3
        if(this.currentPage > 5 && this.maxPage - this.currentPage > 5) return 3;
        if(this.currentPage < 8 && this.maxPage - this.currentPage > 2) return 2;
        return 4;
      }
    },
    methods: {
      setCurrentPage: function(page){
        if(!this.showList) return;
        this.currentPage = page;
        if(this.pagesStatus == 1 || this.pagesStatus == 2){
          var index = page - 1;
        }else if(this.pagesStatus == 3){
          var index = 4;
        }else{
          var index = 8 - (this.maxPage - page)
        }
        for(var i = 0;i < this.pageFlag.length;i++){
          if(i == index){
            this.pageFlag[i] = true;
          }else{
            this.pageFlag[i] = false;
          }
        }
        this.getData();
      },
      changeCurrentPage: function(num){
        if(num > 0){
          if(this.currentPage == this.maxPage) return;
          this.currentPage++;
          this.setCurrentPage(this.currentPage);
        }else if(num < 0){
          if(this.currentPage == 1) return;
          this.currentPage--;
          this.setCurrentPage(this.currentPage);
        }
      },
      arrayTransform: function(arr){
        var str = '';
        for(var i = 0;i < arr.length;i++){
          if(i !== arr.length - 1){
            str = str + arr[i] + '/';
          }else{
            str = str + arr[i];
          }
        }
        return str;
      },
      getData: function(){
        this.showList = false;
        this.movies = [];
        var start = this.firstRank - 1;
        var count = this.pageItems;
        this.$jsonp('https://douban.uieee.com/v2/movie/top250&start=' + start + '&count=' + count)
        .then(res => {
          this.showList = true;
          this.movies = res.subjects;
          this.total = res.total;
          this.showPages = true;
        })
      }
    },
    created: function(){
      this.getData();
    }
  }
</script>