<template>
  <div class="container">
    <p class="video-rec">视频推荐</p>
    <div class="video-list" v-show='list'>
      <router-link :to="{path: 'detail' , query: {src: item.mp4_url}}" class="video-list-item" v-for='item,index in list' :key='index'>
        <img :src="item.cover">
        <div class="video-inf">
          <p class="video-desc">{{item.title}}</p>
          <div class="video-inf-bottom">
            <span class="video-source">{{item.videosource}}</span>
            <span class="video-time">{{timeTransform(item.ptime)}}</span>
          </div>
        </div>
      </router-link>
    </div>
    <div class="index-loading" v-show='loading'>加载中......</div>
    <div class="index-loading" v-show='error'>加载失败</div>
  </div>
</template>
<style>
  .video-rec{
    margin-top: 20px;
    font-size: 30px;
    padding-bottom: 5px;
    border-bottom: 1px solid #ccc;
    margin-bottom: 20px;
  }
  .video-list{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .video-list-item{
    display: flex;
    width: 353px;
    box-sizing: border-box;
    padding: 5px;
    margin-bottom: 10px;
  }
  .video-list-item:hover{
    background-color: rgba(0,0,0,.1);
  }
  .video-list-item>img{
    width: 160px;
    height: 90px;
  }
  .video-inf{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 173px;
    margin-left: 10px;
  }
  .video-desc{
    font-size: 14px;
    color: #37a;
  }
  .video-inf-bottom{
    font-size: 12px;
    display: flex;
  }
  .video-source{
    color: #333;
    max-width: 80px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: block;
  }
  .video-time{
    margin-left: 3px;
    color: #666;
  }
</style>
<script>
  export default{
    data(){
      return{
        list: [],
        loading: false,
        error: false
      }
    },
    methods: {
      timeTransform: function(str){
        var thisDate = new Date();
        var date = new Date(str);
        var timeStr = "";
        var thisHour = thisDate.getHours();
        var hour = date.getHours();
        var thisMinute = thisDate.getMinutes();
        var minute = date.getMinutes();
        if(thisHour < 10){
          thisHour = "0" + thisHour;
        }
        if(hour < 10){
          hour = "0" + hour;
        }
        if(thisMinute < 10){
          thisMinute = "0" + thisMinute;
        }
        if(minute < 10){
          minute = "0" + minute;
        }
        var space = (thisDate - date) / 1000;
        if(space < 60){
          if(thisDate.getSeconds() > date.getSeconds()){
            return timeStr = (thisDate.getSeconds() - date.getSeconds()) + "秒前";
          }else if(thisDate.getSeconds() < date.getSeconds()){
            return timeStr = (thisDate.getSeconds() + 60 - date.getSeconds()) + "秒前";
          }else{
            return timeStr = "1秒前";
          }
        }

        if(space / 3600 < 1){
          if(thisDate.getMinutes() > date.getMinutes()){
            return timeStr = (thisDate.getMinutes() - date.getMinutes()) + "分钟前";
          }else{
            return timeStr = (thisDate.getMinutes() + 60 - date.getMinutes()) + "分钟前";
          }
        }

        if(space / 3600 / 24 < 1 && thisDate.getDay() == date.getDay()){
          return timeStr = hour + ":" + minute; 
        }
        if(space / 3600 / 24 < 2 && (thisDate.getDay() - date.getDay() == 1 || thisDate.getDay() - date.getDay() == -6)){
          return timeStr = "昨天" + hour + ":" + minute;
        }
        if((space / 3600 / 24 > 2 && thisDate.getFullYear() == date.getFullYear()) || ((space / 3600 / 24 > 1 && thisDate.getDay() - date.getDay() !== 1 && thisDate.getDay() - date.getDay() !== -6) && thisDate.getFullYear() == date.getFullYear())){
          return timeStr = (date.getMonth() + 1) + "月" + date.getDate() + "日 " + hour + ":" + minute;
        }
        return timeStr = date.getFullYear() + "年" + (date.getMonth() + 1) + "月" + date.getDate() + "日";
        
        // 一分钟内
        // 一小时内
        // 今天
        // 昨天
        // 两天前 - 年头
        // 去年
      }
    },
    created: function(){
      this.loading = true;
      this.$jsonp('https://3g.163.com/touch/nc/api/video/recommend/Video_Recom/0-12.do')
      .then(res => {
        this.list = res.Video_Recom;
        this.loading = false;
      }).catch(err => {
        this.error = true;
        this.loading = false;
      })
    }
  }
</script>