<template>
  <div class="container music-search">
    <music-search @search='getData' :keyword='keyword'></music-search>
    <p class="music-search-caption" v-if='hasSearched'>搜索结果</p>
    <music-list :songs='songs' v-if='hasSearched'></music-list>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        songs: {
          list: [],
          rank: false,
          album: false
        },
        hasSearched: false,
        keyword: ""
      }
    },
    methods: {
      getData: function(keyword){
        this.$axios.get('https://api.apiopen.top/searchMusic?name=' + keyword)
        .then(res => {
          this.songs.list = res.data.result
          this.hasSearched = true;
        })
      }
    },
    created: function(){
      if(this.$route.params.name){
        this.getData(this.$route.params.name)
        this.keyword = this.$route.params.name
      }
    }
  }
</script>
<style>
  .music-search-caption{
    font-size: 30px;
    padding-bottom: 5px;
    border-bottom: 1px solid #ccc;
    margin-bottom: 26px;
  }
  .music-search{
    margin-bottom: 20px;
  }
</style>