<template>
  <div class="container">
    <div class="movie-caption movie-caption-ua">
      <p>北美票房榜</p>
      <span class="movie-ua-date">日期范围：{{date}}</span>
    </div>
    <div class="movie-rank-list" v-show='showList'>
      <ul>
        <li v-for='movie,index in movies' :key='index'>
          <p class="movie-rank-rank">{{movie.rank || index + 1}}</p>
          <router-link :to="{path:'movieDetail' , query:{id: movie.subject.id}}" class="movie-rank-cover"><img :src="'https://images.weserv.nl/?url=' + movie.subject.images.small"></router-link>
          <div class="movie-rank-inf">
            <router-link :to="{path:'movieDetail' , query:{id: movie.subject.id}}" class="movie-rank-title">{{movie.subject.title}}</router-link>
            <p class="movie-rank-pubdate">上映日期：{{arrayTransform(movie.subject.pubdates)}}</p>
            <p class="movie-rank-genres">类型：{{arrayTransform(movie.subject.genres)}}</p>
            <div class="movie-rank-casts">
              <span>导演：</span>
              <span class="movie-casts-name" v-for='director,idx in movie.subject.directors' :key='idx' v-if='idx == 0'>
                <router-link :to="{path: 'artistDetail' , query: {id: director.id}}">{{director.name}}</router-link>
              </span>
              <span class="movie-casts-name" v-else>
                &nbsp;,&nbsp;<router-link :to="{path: 'artistDetail' , query: {id: director.id}}">{{director.name}}</router-link>
              </span>
            </div>
            <div class="movie-rank-casts">
              <span>主演：</span>
              <span class="movie-casts-name" v-for='actor,id in movie.subject.casts' :key='id' v-if='id == 0'>
                <router-link :to="{path: 'artistDetail' , query: {id: actor.id}}">{{actor.name}}</router-link>
              </span>
              <span class="movie-casts-name" v-else>
                &nbsp;,&nbsp;<router-link :to="{path: 'artistDetail' , query: {id: actor.id}}">{{actor.name}}</router-link>
              </span>
            </div>
            <div class="movie-rank-score" v-if='movie.subject.rating.average !== 0'>
              <movie-score :score='movie.subject.rating.average'></movie-score>
              <p class="movie-rank-people">({{movie.subject.collect_count}}评)</p>
            </div>
            <p class="movie-rank-score" v-else>暂无评分</p>
          </div>
        </li>
      </ul>
      <a href="#" class="movie-load-more" v-show='ifLoadMore.more' @click.prevent='loadMore()'>查看更多</a>
      <p class="movie-loading" v-show='ifLoadMore.loading'>加载中。。。</p>
    </div>
    <p class="movie-list-loading" v-show='!showList'>加载中...</p>
  </div>
</template>
<style>
  .movie-caption-ua{
    margin-bottom: 0px;
    display: flex;
  }
  .movie-ua-date{
    display: block;
    margin-left: 10px;
    font-size: 14px;
    color: #333;
    padding-top: 20px;
  }
  .movie-rank-list{
    padding: 0 20px;
  }
  .movie-rank-list li{
    display: flex;
    padding-bottom: 10px;
    padding-top: 10px;
    border-bottom: 1px solid #ccc;
    align-items: center;
  }
  .movie-rank-rank{
    font-size: 40px;
    text-align: center;
    padding: 0 20px;
    height: 100px;
    line-height: 100px;
    width: 90px;
  }
  .movie-rank-cover{
    display: block;
    margin-top: 3px;
  }
  .movie-rank-cover img{
    border: 1px solid #ccc;
    width: 84px;
    height: 120px;
    display: block;
  }
  .movie-rank-inf{
    width: 750px;
    margin-left: 40px;
    color: #000;
  }
  .movie-rank-inf a:hover{
    text-decoration: underline;
  }
  .movie-rank-title{
    font-size: 18px;
    color: #000;
  }
  .movie-rank-casts a{
    color: #000;
  }
  .movie-rank-casts{
    display: flex;
  }
  .movie-rank-casts span{
    display: block;
  }
  .movie-rank-score{
    display: flex;
    margin-top: 2px;
  }
  .movie-rank-people{
    font-size: 14px;
    margin-left: 5px;
    color: #666;
  }
  .movie-load-more,.movie-loading{
    display: block;
    width: 100%;
    text-align: center;
    padding: 10px 0;
    background-color: rgba(0,0,0,.4);
    margin-top: 20px;
    color: #fff;
    transition: all .2s;
  }
  .movie-load-more:hover,.movie-loading{
    background-color: rgba(0,0,0,.2);
    color: #000;
  }
  .movie-list-loading{
    font-size: 18px;
    margin-top: 20px;
    text-align: center;
  }
</style>
<script>
  export default{
    data(){
      return{
        movies: [],
        count: 10,
        basicItems: 20,
        total: 0,
        ifLoadMore: {
          more: false,
          loading: false
        },
        showList: false,
        date: ''
      }
    },
    methods: {
      arrayTransform: function(arr){
        var str = '';
        for(var i = 0;i < arr.length;i++){
          if(i !== arr.length - 1){
            str = str + arr[i] + '/';
          }else{
            str = str + arr[i];
          }
        }
        return str;
      },
      loadMore: function(){
        this.ifLoadMore.loading = true;
        this.ifLoadMore.more = false;
        this.getData(this.currentCity);
      },
      getData: function(){
        var start = this.movies.length;
        var count = this.count;
        if(start == 0){
          count = this.basicItems;
        }
        this.$jsonp('https://douban.uieee.com/v2/movie/us_box&start=' + start + '&count=' + count)
        .then(res => {
          this.date = res.date;
          this.showList = true;
          this.ifLoadMore.loading = false;
          for(var i = 0;i < res.subjects.length;i++){
            this.movies.push(res.subjects[i])
          }
          this.total = res.total;
          if(this.movies.length < this.total){
            this.ifLoadMore.more = true;
          }
        })
      }
    },
    created: function(){
      this.getData();
    }
  }
</script>