import Vue from 'vue'
import App from './App.vue'
import axios from 'axios'
import VueRouter from 'vue-router';
import Vuex from 'vuex'
import VueJsonp from 'vue-jsonp'

Vue.use(VueRouter);
Vue.use(Vuex);
Vue.use(VueJsonp);
Vue.prototype.$axios = axios;
axios.defaults.baseURL = "http://127.0.0.1/";

import Index from './components/index/index.vue'
import Book from './components/book/book.vue'
import Movie from './components/movie/movie.vue'
import ArtistDetail from './components/movie/artistDetail.vue'
import MovieDetail from './components/movie/movieDetail.vue'
import MovieHot from './components/movie/hot.vue'
import MovieComing from './components/movie/coming.vue'
import MovieTop from './components/movie/top250.vue'
import MovieWeekly from './components/movie/weekly.vue'
import MovieNA from './components/movie/na.vue'
import MovieNew from './components/movie/new.vue'
import Score from './components/movie/score.vue'
import Music from './components/music/music.vue'
import News from './components/news/news.vue'
import Video from './components/video/video.vue'
import VideoList from './components/video/videoList.vue'
import VideoDetail from './components/video/detail.vue'
import BookDetail from './components/book/detail.vue'
import Rank from './components/music/rank.vue'
import Search from './components/music/search.vue'
import Broadcast from './components/music/broadcast.vue'
import BroadcastDetail from './components/music/broadcastDetail.vue'
import MusicIndex from './components/music/index/index.vue'
import MusicSearch from './components/music/index/search.vue'
import MusicList from './components/music/list.vue'

Vue.component('music-search',MusicSearch)
Vue.component('music-list',MusicList)
Vue.component('movie-score',Score)

let router = new VueRouter({
	routes:[
		{path: '/', redirect: '/index'},
    {path: '/index', component: Index},
    {path: '/book', component: Book},
    {path: '/book/detail', component: BookDetail, name: 'BookDetail'},
    {path: '/movie', component: Movie, 
      children:[
        {path: '', redirect: 'hot'},
        {path: 'hot', component: MovieHot},
        {path: 'coming', component: MovieComing},
        {path: 'top250', component: MovieTop},
        {path: 'weekly', component: MovieWeekly},
        {path: 'NA', component: MovieNA},
        {path: 'new', component: MovieNew},
        {path: 'movieDetail', component: MovieDetail},
        {path: 'artistDetail', component: ArtistDetail},
      ]
    },
    {path: '/music', component: Music, 
     children: [
      {path: '', redirect: 'index'},
      {path: 'search', component: Search, name: 'musicSearch'},
      {path: 'rank', component: Rank, name: 'musicRank'},
      {path: 'broadcast', component: Broadcast},
      {path: 'index', component: MusicIndex},
      {path: 'broadcast/detail' , component: BroadcastDetail, name:'BroadcastDetail'}
     ]
    },
    {path: '/news', component: News},
    {path: '/video', component: Video, redirect: '/video/list',
      children:[
        {path: 'list', component: VideoList},
        {path: 'detail', component: VideoDetail}
      ]
    }
	]  
})


new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



