<template>
  <div>
    <p class="music-index-rank-caption">排行榜</p>
    <ul class="music-index-rank" v-if='charts'>
      <li v-for='chart,index in charts'>
        <router-link :to="{name: 'musicRank' , params: {index : index}}">
          <img :src="chart.pic_s192">
          <div class="music-index-rank-inf">
            <a class="music-index-rank-name">{{chart.name}}</a>
            <p class="music-index-rank-des">{{chart.comment}}</p>
          </div>
        </router-link>
      </li>
    </ul>
    <div class="index-loading" v-if='loading'>加载中......</div>
    <div class="index-loading" v-if='error'>加载失败</div>
  </div>
</template>
<style>
  .music-index-rank-caption{
    font-size: 30px;
    padding-bottom: 5px;
    border-bottom: 1px solid #ccc;
    margin-bottom: 20px;
  }
  .music-index-rank{
    padding: 0 20px;
    display: flex;
    width: 100%;
    box-sizing: border-box;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .music-index-rank>li{
    width: 33%;
  }
  .music-index-rank>li>a{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    padding: 16px;
  }
  .music-index-rank>li>a:hover{
    background-color: #eee;
  }
  .music-index-rank>li>a>img{
    width: 150px;
    height: 150px;
  }
  .music-index-rank-inf{
    width: 145px;
  }
  .music-index-rank-name{
    color: #72e;
  }
  .music-index-rank-name:hover{
    text-decoration: underline;
  }
  .music-index-rank-des{
    margin-top: 10px;
    font-size: 14px;
    color: #000;
  }
</style>
<script>
  export default{
    data(){
      return{
        charts: {},
        loading: false,
        error: false
      }
    },
    created: function(){
      this.loading = true;
      this.$axios.get('https://api.apiopen.top/musicRankings')
      .then(res => {
        this.charts = res.data.result;
        this.loading = false;
      }).catch(err => {
        this.error = true;
        this.loading = false;
      })
    }
  }
</script>