<template>
  <div>
    <div class="title">
      <router-link :to="{path: '/book'}">豆瓣小说</router-link>
    </div>
    <div class="books" v-if='book[0]'>
      <div class="book" v-for='item,index in book' :key='index'>
        <router-link :to="{path: '/book/Detail',query:{name:item.bookname}}"><img :src="item.book_cover"></router-link>
        <router-link :to="{path: '/book/Detail',query:{name:item.bookname}}" class="book-name" :title="item.bookname">{{item.bookname}}</router-link>
        <p class="book-author" :title="item.author_name">{{item.author_name}}</p>
      </div>
    </div>
    <div class="index-loading" v-if='loading'>加载中......</div>
    <div class="index-loading" v-if='error'>加载失败</div>
  </div>
</template>
<style>
  .books{
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
  }
  .book img{
    width: 180px;
    height: 240px;
    border: 1px solid #ccc;
  }
  .book{
    display: flex;
    flex-direction: column;
  }
  .book-name{
    font-size: 18px;
    display: block;
    text-align: center;
    color: #3377aa;
    margin-top: 5px;
    margin-bottom: 3px;
  }
  .book-name:hover{
    color: #072;
  }
  .book-author{
    text-align: center;
    width: 100%;
  }
  .book-name,.book-author{
    max-width: 180px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
</style>
<script>
  export default{
    data(){
      return{
        book: {},
        loading: false,
        error: false
      }
    },
    created: function(){
      var that = this;
      that.loading = true;
      this.$axios.get('https://www.apiopen.top/novelApi')
      .then(function(res){
        that.book = res.data.data;
        that.loading = false;
      }).catch(function(error){
        that.loading = false;
        that.error = true;
      })
    }
  }
</script>