<template>
  <div class="container music-chart">
    <div class="rank-cats">
      <a href="#" v-for='chart,index in charts' :key='index' @click.prevent='getData(index)' :class="{'current-chart' : chartFlags[index].flag}">{{chart.name}}</a>
    </div>
    <music-list :songs='list' v-show='showList'></music-list>
  </div>
</template>
<style>
  .music-chart{
    margin-bottom: 20px;
  }
  .rank-cats{
    display: flex;
    justify-content: space-between;
    border-radius: 30px;
    background-color: #ccc;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .rank-cats a{
    display: block;
    padding: 14px 6px;
    width: 100%;
    text-align: center;
    color: #333;
  }
  .rank-cats a:hover{
    color: red;
  }
  .rank-cats .current-chart{
    color: red;
  }
</style>
<script>
  export default{
    data(){
      return{
        list: {
          list: [],
          rank: true,
          album: true
        },
        charts: [],
        chartFlags: [],
        showList: false,
        listFlag: true
      }
    },
    methods: {
      getData: function(index){
        if(this.listFlag){
          document.cookie = "index=" + index
          this.listFlag = false;
          this.showList = false;
          for(var i = 0;i < this.chartFlags.length;i++){
            this.chartFlags[i].flag = false;
          }
          this.chartFlags[index].flag = true;
          var type = this.charts[index].type
          var arr = [];
          this.$axios.get('https://api.apiopen.top/musicRankingsDetails?type=' + type)
          .then(res => {
            var result = res.data.result;
            for(var i = 0;i < result.length;i++){
              var obj = {}
              obj.rank = result[i].rank;
              obj.title = result[i].title;
              obj.author = result[i].author;
              obj.album = result[i].album_title;
              arr.push(obj)
            }
            this.list.list = arr;
            this.listFlag = true;
            this.showList = true;
          })
        }
      }
    },
    created: function(){
      var index = 0
      if(document.cookie.split(';')[0].split('=')[1] || this.$route.params.index){
        index = parseInt(document.cookie.split(';')[0].split('=')[1]) || this.$route.params.index;
      }
      this.$axios.get('https://api.apiopen.top/musicRankings')
      .then(res => {
        var result = res.data.result;
        var arr = [];
        for(var i = 0;i < result.length;i++){
          var obj = {};
          obj.name = result[i].name;
          obj.type = result[i].type;
          arr.push(obj)
          this.$set(this.chartFlags,i,{flag: false})
        }
        this.charts = arr;
        this.getData(index);
        this.showList = true;
      })
    },
    beforeDestroy: function(){
      document.cookie = "index = ;  expires=Thu, 01 Jan 1970 00:00:01 GMT;"
    }
  }
</script>