<template>
  <div class="photos-slide" @click.prevent='showSlide()'>
    <div class="photos-show" :style="{height: photoShowHeight + 'px'}">
      <a href="#" class="photos-left" @click.prevent.stop='changePhoto(-1)' :style="{top: photosBtnTop + 'px'}">
        <i class="icofont-simple-left"></i>
      </a>
      <img v-for='item,index in photos' :key='index' :src="'https://images.weserv.nl/?url=' + item.image" v-show='showPhotos[index].show' @click.stop>
      <a href="#" class="photos-right" @click.prevent.stop='changePhoto(1)' :style="{top: photosBtnTop + 'px'}">
        <i class="icofont-simple-right"></i>
      </a>
    </div>
    <div class="photos-page">
      <span @click.stop>{{current + 1}} / {{total}}</span>
    </div>
    <div class="photos-icons-bgc" @click.stop>
      <div class="photos-icons" id='overall'>
        <div class="photos-current-outline" id="outline" :style="{'left' : firstPosition + 'px'}"></div>
        <ul :style="{'left' : distance + 'px'}">
          <li v-for='item,index in photos' :key='index'>
            <a href="#" :style="{'background-image' : 'url(https://images.weserv.nl/?url=' + item.cover + ')'}" @click.prevent='selectPhoto(index)'></a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<style>
  .photos-slide{
    position: fixed;
    width: 100%;
    background-color: rgba(0,0,0,.5);
    top: 0;
    left: 0;
  }
  .photos-show{
    display: flex;
    position: relative;
    vertical-align: middle;
    width: 810px;
    margin: 0 auto;
    padding: 20px 0;
  }
  .photos-show img{
    display: block;
    margin: auto;
    max-width: 100%;
    max-height: 100%;
  }
  .photos-show a{
    position: absolute;
    font-size: 50px;
    color: #eee;
  }
  .photos-show a:hover{
    color: #fff;
  }
  .photos-left{
    left: -80px;
  }
  .photos-right{
    right: -80px;
  }
  .photos-page{
    font-size: 20px;
    height: 30px;
    line-height: 30px;
    color: #fff;
    text-align: center;
  }
  .photos-page span{
    padding: 0 15px;
  }
  .photos-icons-bgc{
    background-color: rgba(0,0,0,.8);
    padding: 11px 100px;
    height: 98px;
  }
  .photos-current-outline{
    position: absolute;
    border: 4px solid blue;
    height: 90px;
    width: 160px;
    top: 0px;
    z-index: 99;
  }
  .photos-icons{
    width: 100%;
    overflow: hidden;
    height: 100%;
    position: relative;
  }
  .photos-icons ul{
    position: absolute;
    display: flex;
    left: 0px;
    padding-top: 4px;
    transition: all .2s;
  }
  .photos-icons ul li{
    margin-right: 20px;
  }
  .photos-icons ul li a{
    display: inline-block;
    height: 90px;
    width: 160px;
    background-size: cover;
  }
</style>
<script>
  export default{
    data(){
      return{
        innerHeight: 0,
        firstPosition: 0,
        total: 0,
        showPhotos: [],
        current: 0
      }
    },
    computed: {
      distance: function(){
        //160 : width   20 : margin-right   4 : outline的border宽度
        return this.firstPosition + 4 - (this.current) * (160 + 20);
      },
      photoShowHeight: function(){
        // 120:底下缩略图高度; 30:页数显示栏的高度; 20:该元素垂直padding的大小
        return this.innerHeight - 120 - 30 - 20*2;
      },
      photosBtnTop: function(){
        // (innerHeight - 120 - 30 - 20*2) / 2 : 父元素高度 / 2 
        // 50 / 2 : 按钮的height / 2
        // 以保证其垂直居中
        return (this.innerHeight - 120 - 30 - 20*2) / 2 - (50 / 2);
      },
    },
    props: ['photos'],
    methods: {
      selectPhoto: function(index){
        this.current = index;
        for(var i = 0;i < this.showPhotos.length;i++){
          if(i == index){
            this.showPhotos[i].show = true;
          }else{
            this.showPhotos[i].show = false;
          }
        }
      },
      changePhoto: function(num){
        if(num > 0){
          if(this.current == this.total - 1) return;
          this.current++;
          this.selectPhoto(this.current)
        }else if(num < 0){
          if(this.current == 0) return;
          this.current--;
          this.selectPhoto(this.current)
        }
      },
      showSlide: function(){
        this.$emit('showSlide',false)
      }
    },
    created: function(){
      this.innerHeight = window.innerHeight;
      for(var i = 0;i < this.photos.length;i++){
        this.$set(this.showPhotos,i,{show : false})
      }
      this.total = this.photos.length;
      this.selectPhoto(this.current);
    },
    mounted: function(){
      this.firstPosition = document.getElementById('overall').offsetWidth / 2 - document.getElementById('outline').offsetWidth / 2;
    }
  }
</script>