<template>
  <div class="container">
    <p class="movie-caption">即将上映</p>
    <div class="movie-list" v-show='showList'>
      <ul>
        <li v-for='movie,index in currentMovies' :key='index'>
          <router-link :to="{path:'movieDetail' , query:{id: movie.id}}" class="movie-cover" :title='movie.title'><img :src="'https://images.weserv.nl/?url=' + movie.images.small"></router-link>
          <router-link :to="{path:'movieDetail' , query:{id: movie.id}}" class="movie-title" :title='movie.title'>{{movie.title}}</router-link>
          <movie-score :score="movie.rating.average" v-if='movie.rating.average !== 0'></movie-score>
          <p v-else class="no-score">暂无评分</p>
          <p class="movie-coming-pubdate">上映日期：{{arrayTransform(movie.pubdates)}}</p>
        </li>
      </ul>
      <a href="#" class="movie-load-more" v-show='ifLoadMore.more' @click.prevent='loadMore()'>查看更多</a>
      <p class="movie-loading" v-show='ifLoadMore.loading'>加载中。。。</p>
    </div>
    <p class="movie-list-loading" v-show='!showList'>加载中...</p>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        currentMovies: [],
        count: 10,
        basicItems: 20,
        total: 0,
        ifLoadMore: {
          more: false,
          loading: false
        },
        showList: false
      }
    },
    methods: {
      loadMore: function(){
        this.ifLoadMore.loading = true;
        this.ifLoadMore.more = false;
        this.getData(this.currentCity);
      },
      getData: function(){
        var start = this.currentMovies.length;
        var count = this.count;
        if(start == 0){
          count = this.basicItems;
        }
        this.$jsonp('https://douban.uieee.com/v2/movie/coming_soon&start=' + start + '&count=' + count)
        .then(res => {
          this.showList = true;
          this.ifLoadMore.loading = false;
          for(var i = 0;i < res.subjects.length;i++){
            this.currentMovies.push(res.subjects[i])
          }
          this.total = res.total;
          if(this.currentMovies.length < this.total){
            this.ifLoadMore.more = true;
          }
        })
      },
      arrayTransform: function(arr){
        var str = '';
        for(var i = 0;i < arr.length;i++){
          if(i !== arr.length - 1){
            str = str + arr[i] + '/';
          }else{
            str = str + arr[i];
          }
        }
        return str;
      }
    },
    created: function(){
      this.getData();
    }
  }
</script>
<style>
  .movie-list{
    padding: 0 30px;
    margin-bottom: 20px;
  }
  .movie-list ul{
    display: flex;
    flex-wrap: wrap;
  }
  .movie-list ul li{
    display: flex;
    flex-direction: column;
    margin-right: 57.5px;
    align-items: center;
    margin-top: 20px;
  }
  .movie-list ul li:nth-child(5n){
    margin-right: 0;
  }
  .movie-cover img{
    width: 160px;
    height: 228px;
    border: 1px solid #ccc;
  }
  .movie-title{
    color: #000;
    max-width: 162px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .no-score{
    font-size: 14px;
  }
  .movie-coming-pubdate{
    font-size: 12px;
    color: #777;
    text-align: center;
    max-width: 162px;
  }
  .movie-load-more,.movie-loading{
    display: block;
    width: 100%;
    text-align: center;
    padding: 10px 0;
    background-color: rgba(0,0,0,.4);
    margin-top: 20px;
    color: #fff;
    transition: all .2s;
  }
  .movie-load-more:hover,.movie-loading{
    background-color: rgba(0,0,0,.2);
    color: #000;
  }
  .movie-list-loading{
    font-size: 18px;
    margin-top: 20px;
    text-align: center;
  }
</style>