<template>
  <div class="container artist" id="container">
    <template v-if='!loading'>
      <p class="artist-name">
        {{artist.fullname}}
      </p>
      <div class="artist-inf">
        <img :src="'https://images.weserv.nl/?url=' + artist.avatars.small" v-if='artist.avatars'>
        <div class="artist-detail">
          <p class="artist-gender" v-if='artist.gender'>性别：{{artist.gender}}</p>
          <p class="artist-constellation" v-if='artist.constellation'>星座：{{artist.constellation}}</p>
          <p class="artist-birthday" v-if='artist.birthday'>生日：{{artist.birthday}}</p>
          <p class="artist-birthplace" v-if='artist.born_place'>出生地：{{artist.born_place}}</p>
          <p class="artist-job" v-if='artist.professions'>职业：{{artist.professions}}</p>
          <p class="artist-aka-en" v-if='artist.aka_en'>更多外文名：{{artist.aka_en}}</p>
          <p class="artist-aka-ch" v-if='artist.aka'>更多中文名：{{artist.aka}}</p>
        </div>
      </div>
      <div class="artist-summary">
        <p class="artist-captions">影人简介</p>
        <div class="artist-summary-content">
          <p :class="{'artist-describe' : true , 'artist-summary-more' : showMore}" id="summary" v-if='artist.summary' v-html='artist.summary'></p>
          <p class="artist-describe" id="summary" v-else>暂无</p>
          <div class="artist-more" v-if='showMore'>
            <a href="#" @click.prevent='showMore = false'>查看更多</a>
          </div>
        </div>
      </div>
      <div class="artist-photos">
        <p class="artist-captions">
          影人图片
        </p>
        <div class="artist-photos-list">
          <a href="#" class="artist-photos-icon" :style="{'background-image': 'url(https://images.weserv.nl/?url=' + item.cover + ')'}" v-for='item,index in  artist.photos' :key='index' @click.prevent='showSlide(true,index)'></a>
        </div>
      </div>
      <div class="artist-works">
        <p class="artist-captions">{{artist.name}}&nbsp;主要作品</p>
        <ul>
          <li v-for='item,index in artist.works' :key='index'>
            <router-link :to="{path: 'movieDetail' , query: {id : item.subject.id}}" class="work-cover">
              <img :src="'https://images.weserv.nl/?url=' + item.subject.images.small" :title='item.subject.title'>
            </router-link>
            <router-link :to="{path: 'movieDetail' , query: {id : item.subject.id}}" class="work-title" :title='item.subject.title'>{{item.subject.title}}</router-link>
            <p class="work-year">{{item.subject.year}}</p>
          </li>
        </ul>
      </div>
    </template>
    <p v-if='loading' class="artist-loading">加载中......</p>
    <slide :photos='artist.photos' v-if='artist.photos' ref='slide' v-show='slide' @showSlide='showSlide'></slide>
  </div>
</template>
<style>
  .artist-captions{
    margin-top: 10px;
    font-size: 28px;
    margin-bottom: 8px;
  }
  .artist{
    padding-top: 20px;
  }
  .artist-name{
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  .artist-inf{
    display: flex;
    justify-content: space-between;
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;
  }
  .artist-inf>img{
    width: 150px;
    height: 213px;
    border: 1px solid #ccc;
  }
  .artist-detail{
    width: 920px;
  }
  .artist-detail p{
    font-size: 18px;
    margin-bottom: 2px;
  }
  .artist-summary{
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;
  }
  .artist-summary-content{
    position: relative;
  }
  .artist-summary-more{
    height: 150px;
    overflow: hidden;
  }
  .artist-more{
    position: absolute;
    text-align: center;
    width: 100%;
    bottom: 0;
    padding: 11px 0;
    background-color: rgba(155,155,155,.9);
  }
  .artist-more a{
    color: #000;
  }
  .artist-more a:hover{
    color: #072;
  }
  .artist-photos{
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;
  }
  .artist-photos-list{
    display: flex;
    flex-wrap: wrap;
  }
  .artist-photos-icon{
    width: 135px;
    height: 112px;
    margin-right: 20px;
    margin-top: 20px;
    display: block;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border: 1px solid #ccc;
  }
  .artist-works{
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
  }
  .artist-works .artist-captions{
    margin-bottom: 20px;
  }
  .artist-works ul{
    display: flex;
    justify-content: space-between;
  }
  .artist-works ul li{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .work-cover img{
    width: 178px;
    height: 238px;
    border: 1px solid #ccc;
  }
  .work-title{
    margin-top: 4px;
    margin-bottom: 4px;
    color: #000;
    max-width: 180px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .work-title:hover{
    text-decoration: underline;
  }
  .work-year{
    color: #666;
    text-align: center;
    font-size: 14px;
  }
  .artist-loading{
    font-size: 30px;
  }
</style>
<script>
  import slide from './photoSlide.vue'
  export default{
    data(){
      return{
        artist: {},
        showMore: false,
        loading: true,
        slide: true
      }
    },
    components: {slide},
    methods: {
      getData: function(id){
        this.loading = true;
        this.$jsonp('http://douban.uieee.com/v2/movie/celebrity/' + id)
        .then(res => {
          this.loading = false;
          this.artist = res;
          this.artist.professions = this.arrayTransForm(this.artist.professions)
          this.artist.aka = this.arrayTransForm(this.artist.aka)
          this.artist.aka_en = this.arrayTransForm(this.artist.aka_en)
          this.artist.summary = this.artist.summary.replace(/\n/g,"<br/>")
          if(this.artist.name && this.artist.name_en){
            this.artist.fullname = this.artist.name + ' ' + this.artist.name_en;
          }
          this.$nextTick(function(){
            this.slide = false;
            if(document.getElementById('summary').offsetHeight > 200){
              this.showMore = true;
            }else{
              this.showMore = false;
            }
          })
        })
      },
      arrayTransForm: function(fakeArr){
        var arr = [];
        //由于接口(professions)数组中有空数据，用此方法去除之
        for(var i = 0;i < fakeArr.length;i++){
          if(fakeArr[i].trim() !== ''){
            arr.push(fakeArr[i])
          }
        }
        var str = '';
        for(var i = 0;i < arr.length;i++){
          if(i == arr.length - 1){
            str = str + arr[i];
          }else{
            str = str + arr[i] + ' / ';
          }
        }
        return str;
      },
      showSlide: function(show,index){
        this.slide = show;
        if(index){
          this.$refs.slide.selectPhoto(index)
        }
      }
    },
    created: function(){
      this.getData(this.$route.query.id);
    },
    watch: {
      $route: function(){
        this.getData(this.$route.query.id);
      }
    },
  }
</script>