<template>
  <div class="novel">
    <label>
      <input type="text" class="novel-search" v-model="input" placeholder="请输入小说名称">
      <a href="#" class="novel-search-btn" @click.prevent='search'>小说搜索</a>
    </label>
    <p v-show='hasSearched'>搜索结果</p>
    <div class="novel-search-result" v-show='results'>
      <router-link :to="{path: 'book/detail',query:{name:item}}" v-for='item,index in results' :key='index'>{{item}}</router-link>
    </div>
    <div class="index-loading" v-show='loading'>加载中......</div>
    <div class="novel-search-result" v-show='hasSearched&&!results&&!loading'>未搜索到数据！！</div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        results: {},
        input: "",
        hasSearched: false,
        loading: false
      }
    },
    methods: {
      search(name){
        if(this.input.trim() !== "" || name){
          if(name){
            this.input = name;
          }
          this.loading = true;
          this.hasSearched = true;
          this.results = {};
          this.$router.push({path: '/book' , query: {name: this.input}})
          var url = 'https://www.apiopen.top/novelSearchApi?name=' + this.input;
          this.$axios.get(url)
          .then(res => {
            this.results = res.data.data;
            this.loading = false;
          }).catch(err => {
            this.loading = false;
          })
        }
      }
    },
    created: function(){
      if(this.$route.query.name){
        this.search(this.$route.query.name)
      }
    },
    mounted: function(){
      var that = this
      var input = document.querySelector('.novel-search');
      input.onkeyup = function(e){
        if(e.keyCode == 13){
          that.search();
          input.blur();
        }
      }
    }
  }
</script>
<style>
  .novel{
    width: 1100px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .novel>label{
    margin: 40px 0;
  }
  .novel-search{
    width: 400px;
    height: 40px;
    border: 0;
    outline: none;
    font-size: 18px;
    font-family: "微软雅黑";
    padding-left: 24px;
    border-radius: 30px;
    background-color: #eee;
    margin-right: 10px;
  }
  .novel-search-btn{
    color: #000;
  }
  .novel-search-btn:hover{
    color: red;
  }
  .novel-search-result{
    width: 900px;
    margin-top: 30px;
    margin-bottom: 20px;
  }
  .novel-search-result a{
    display: inline-block;
    margin-right: 15px;
    margin-bottom: 8px;
    color: #07a;
  }
  .novel-search-result a:hover{
    color: #073;
  }
</style>