<template>
  <div class="container">
    <div class="movie-caption movie-caption-hot">
      <p>正在热映 - {{currentCity}}</p>
      <div class="city-change">
        <a href="#" @click.prevent='showCityList(true,$event)'>切换城市</a>
        <div class="city-list" v-show='cityList'>
          <div class="city-list-top">
            <a href="#" v-for='item,index in city' :key='index' @click.prevent='getCurrentCities(index,$event)' :class="{'current-cat' : item.flag}">{{item.cat}}</a>
          </div>
          <ul class="city-list-bottom" v-for='cities,idx in currentCities' :key='idx'>
            <li class="city-first-letter" v-show='cities.letter'>{{cities.letter}}</li>
            <li v-for='city,id in currentCities[idx].cities' :key='id'>
              <a href="#" @click.prevent='changeCity(city)'>{{city}}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="movie-list" v-show='showList'>
      <ul>
        <li v-for='movie,index in currentMovies' :key='index'>
          <router-link :to="{path:'movieDetail' , query:{id: movie.id}}" class="movie-cover" :title='movie.title'><img :src="'https://images.weserv.nl/?url=' + movie.images.small"></router-link>
          <router-link :to="{path:'movieDetail' , query:{id: movie.id}}" class="movie-title" :title='movie.title'>{{movie.title}}</router-link>
          <movie-score :score="movie.rating.average" v-if='movie.rating.average !== 0'></movie-score>
          <p v-else class="no-score">暂无评分</p>
        </li>
      </ul>
      <a href="#" class="movie-load-more" v-show='ifLoadMore.more' @click.prevent='loadMore()'>查看更多</a>
      <p class="movie-loading" v-show='ifLoadMore.loading'>加载中。。。</p>
    </div>
    <p class="movie-list-loading" v-show='!showList'>加载中...</p>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        city: [
          {
            cat: "热门",
            id: 0,
            flag: false,
            list: [
              {
                letter: false,
                cities:["北京","上海","广州","深圳","成都","武汉","杭州","重庆","郑州","南京","西安","苏州","长沙","天津","福州","济南","沈阳","合肥","青岛","哈尔滨","温州","厦门","大连","东莞","长春"
                ]
              }
            ]
          },{
            cat: "A-G",
            id: 1,
            flag: false,
            list: [
              {
                letter: "A",
                cities: ["安庆","安阳","鞍山","安康","安顺","阿勒泰","阿克苏","阿坝"]
              },{
                letter: "B",
                cities: ["北京","保定","宝鸡","包头","滨州","蚌埠","亳州","毕节","本溪","北海","巴彦淖尔","白银","保山","白色","白山","巴中","巴音郭楞","白城","保停"]
              },{
                letter: "C",
                cities: ["重庆","成都","长沙","长春","常州","郴州","常德","滁州","沧州","长治","赤峰","承德","朝阳","潮州","昌吉","池州","崇左","楚雄","常熟","澄迈","昌江"]
              },{
                letter: "D",
                cities: ["东莞","大连","大庆","东营","德阳","德州","大同","达州","丹东","大理","定西","德宏","儋州","东方","大兴安岭","迪庆"]
              },{
                letter: "E",
                cities: ["恩施","鄂尔多斯","鄂州"]
              },{
                letter: "F",
                cities: ["佛山","福州","抚州","阜阳","抚顺","防城港","阜新"]
              },{
                letter: "G",
                cities: ["广州","赣州","贵阳","桂林","广元","广安","甘南","固原","贵港","甘孜"]
              }
            ]
          },{
            cat: "H-L",
            id: 2,
            flag: false,
            list: [
              {
                letter: "H",
                cities: ["杭州"]
              },{
                letter: "J",
                cities: ["金华","济南"]
              },{
                letter: "K",
                cities: ["昆明"]
              },{
                letter: "L",
                cities: ["临沂","兰州","洛阳"]
              }
            ]
          },{
            cat: "M-T",
            id: 3,
            flag: false,
            list: [
              {
                letter: "M",
                cities: ["绵阳"]
              },{
                letter: "O",
                cities: ["南京","宁波"]
              },{
                letter: "P",
                cities: ["平顶山"]
              },{
                letter: "Q",
                cities: ["青岛","泉州","衢州"]
              },{
                letter: "R",
                cities: ["日照"]
              },{
                letter: "S",
                cities: ["上海","深圳"]
              },{
                letter: "T",
                cities: ["天津","台州","泰州"]
              }
            ]
          },{
            cat: "W-Z",
            id: 4,
            flag: false,
            list: [
              {
                letter: "W",
                cities: ["武汉"]
              },{
                letter: "X",
                cities: ["西安","厦门"]
              },{
                letter: "Y",
                cities: ["烟台"]
              },{
                letter: "Z",
                cities: ["郑州","中山","镇江"]
              }
            ]
          }
        ],
        currentCity: '',
        currentCities: [],
        currentMovies: [],
        cityList: false,
        count: 10,
        basicItems: 20,
        total: 0,
        ifLoadMore: {
          more: false,
          loading: false
        },
        showList: false
      }
    },
    methods: {
      changeCity: function(city){
        this.showCityList(false,false);
        this.ifLoadMore.more = false;
        if(this.currentCity == city) return;
        this.currentMovies = [];
        this.getData(city);
      },
      loadMore: function(){
        this.ifLoadMore.loading = true;
        this.ifLoadMore.more = false;
        this.getData(this.currentCity);
      },
      getData: function(name){
        this.currentCity = name;
        var start = this.currentMovies.length;
        var count = this.count;
        if(start == 0){
          count = this.basicItems;
          this.showList = false;
          this.$router.push({path: '/movie/hot' , query: {name: name}})
        }
        this.$jsonp('https://douban.uieee.com/v2/movie/in_theaters?city=' + name + '&start=' + start + '&count=' + count)
        .then(res => {
          this.showList = true;
          this.ifLoadMore.loading = false;
          for(var i = 0;i < res.subjects.length;i++){
            this.currentMovies.push(res.subjects[i])
          }
          this.total = res.total;
          if(this.currentMovies.length < this.total){
            this.ifLoadMore.more = true;
          }
        })
      },
      getCurrentCities: function(index,event){
        for(var i = 0;i < this.city.length;i++){
          if(i == index){
            this.city[i].flag = true;
          }else{
            this.city[i].flag = false;
          }
        }
        if(event){
          event.stopPropagation();
        }
        this.currentCities = this.city[index].list;
      },
      showCityList: function(flag,event){
        if(flag){
          this.cityList = true;
          var that = this;
          if(event){
            event.stopPropagation();
          }
          window.onclick = function(e){
            that.cityList = false;
          }
        }else{
          this.cityList = false;
        }
      }
    },
    watch: {
      cityList: function(){
        if(this.cityList == false){
          window.onclick = function(e){
            
          }
        }
      }
    },
    created: function(){
      if(this.$route.query.name){
        this.getData(this.$route.query.name)
      }else{
        this.getData('广州')
      }
      this.currentCities = this.city[0].list
      this.getCurrentCities(0,false);
    }
  }
</script>
<style>
  .movie-caption-hot{
    display: flex;
  }
  .city-change{
    margin-left: 20px;
    position: relative;
  }
  .city-change>a{
    font-size: 16px;
    color: #27a;
  }
  .city-list{
    padding: 20px 20px;
    position: absolute;
    width: 335px;
    background-color: #fff;
    z-index: 9999;
    border: 1px solid #ccc;
  }
  .city-list a{
    font-size: 14px;
    display: block;
    margin-right: 10px;
    color: #27a;
  }
  .city-change>a:hover,.city-list a:hover,.city-change .current-cat{
    color: #fff;
    background-color: #27a;
  }
  .city-change>a:active,.city-list a:active{
    background-color: #f93;
    color: #fff;
  }
  .city-list-top{
    display: flex;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
    margin-bottom: 5px;
  }
  .city-list-bottom{
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid #ccc;
    padding-bottom: 10px;
  }
  .city-list-bottom a{
    margin-top: 5px;
  }
  .city-first-letter{
    width: 100%;
    font-size: 16px;
    margin-top: 5px;
  }
  .movie-list{
    padding: 0 30px;
    margin-bottom: 20px;
  }
  .movie-list ul{
    display: flex;
    flex-wrap: wrap;
  }
  .movie-list ul li{
    display: flex;
    flex-direction: column;
    margin-right: 57.5px;
    align-items: center;
    margin-top: 20px;
  }
  .movie-list ul li:nth-child(5n){
    margin-right: 0;
  }
  .movie-cover img{
    width: 160px;
    height: 228px;
    border: 1px solid #ccc;
  }
  .movie-title{
    color: #000;
    max-width: 162px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .no-score{
    font-size: 14px;
  }
  .movie-load-more,.movie-loading{
    display: block;
    width: 100%;
    text-align: center;
    padding: 10px 0;
    background-color: rgba(0,0,0,.4);
    margin-top: 20px;
    color: #fff;
    transition: all .2s;
  }
  .movie-load-more:hover,.movie-loading{
    background-color: rgba(0,0,0,.2);
    color: #000;
  }
  .movie-list-loading{
    font-size: 18px;
    margin-top: 20px;
    text-align: center;
  }
</style>