<template>
  <div>
    <p class="music-index-broadcast-caption">音乐电台</p>
    <div class="music-index-broadcast" v-for="broadcast,index in broadcasts" :key='index' v-if='broadcasts'>
      <p>{{broadcast.name}}</p>
      <div class="music-index-broadcast-channel">
        <router-link :to="{name: 'BroadcastDetail', params: {src: item.thumb?item.thumb:item.avatar , channel: item.cate_sname?item.cate_sname:'音乐人频道' , ch_name: item.ch_name?item.ch_name:item.name , name: item.name}}" v-for='item,idx in broadcast.list' :key='idx' :title='item.name'>
          <img v-if="item.thumb" :src="item.thumb">
          <img v-else :src="item.avatar">
          <p class="music-index-broadcast-name">{{item.name}}</p>
        </router-link>
      </div>
    </div>
    <div class="index-loading" v-show='loading'>加载中......</div>
    <div class="index-loading" v-show='error'>加载失败</div>
  </div>
</template>
<style>
  .music-index-broadcast-caption{
    font-size: 30px;
    padding-bottom: 5px;
    border-bottom: 1px solid #ccc;
    margin-bottom: 20px;
  }
  .music-index-broadcast{
    margin-left: 10px;
    margin-bottom: 20px;
  }
  .music-index-broadcast>p{
    font-size: 18px;
    margin-bottom: 5px;
    border-left: 5px solid #072;
    padding-left: 6px;
  }
  .music-index-broadcast-channel{
    display: flex;
    flex-wrap: wrap;
  }
  .music-index-broadcast-channel>a{
    display: block;
    width: 110px;
    margin-left: 10px;
    padding-bottom: 10px;
  }
  .music-index-broadcast-channel>a:hover .music-index-broadcast-name{
    color: #072;
  }
  .music-index-broadcast-channel>a>img{
    height: 90px;
    width: 90px;
    margin-top: 10px;
    margin-left: 10px;
    border-radius: 50%;
  }
  .music-index-broadcast-name{
    text-align: center;
    color: #000;
  }
</style>
<script>
  export default{
    data(){
      return{
        broadcasts: [],
        loading: false,
        error: false
      }
    },
    created: function(){
      this.loading = true;
      this.$axios.get('https://api.apiopen.top/musicBroadcasting')
      .then(res => {
        var data = res.data.result[0].channellist;
        var arr = [];
        var index = 0;
        var findAttr = function(attr){
          var flag = false;
          for(var j = 0;j < arr.length;j++){
            if(arr[j].name == attr){
              flag = j;
              break;
            }
          }
          return flag;
        }
        for(var i = 0;i < data.length;i++){
          if(findAttr(data[i].cate_sname) === false){
            arr[index] = {};
            arr[index].name = data[i].cate_sname;
            arr[index].list = [];
            arr[index].list.push(data[i])
            index++;
          }else{
            arr[findAttr(data[i].cate_sname)].list.push(data[i])
          }
        }
        var star = {};
        var starData = res.data.result[1]
        star.name = starData.title;
        star.list = starData.channellist;
        arr.push(star)
        this.broadcasts = arr;
        this.loading = false;
      }).catch(err => {
        this.error = true;
        this.loading = false;
      })
    }
  }
</script>