<template>
  <div id="video">
    <div class="title">
      <router-link :to="{path: '/video'}">豆瓣视频</router-link>
    </div>
    <div class="slide" v-if='video[0]'>
      <a href="#" class="slide-left" @click.prevent="slide(-1,1)"><i class="icofont-simple-left"></i></a>
      <a href="#" class="slide-right" @click.prevent="slide(1,1)"><i class="icofont-simple-right"></i></a>
      <ul id="slide_area" :style="{transform : translateX,transition : transition}">
        <li v-for='item,index in video' :key='index' :title='item.title'>
          <a href="#"><img :src="item.cover"></a>
          <a href="#" class="video-title">{{item.title}}</a>
        </li>
      </ul>
    </div>
    <div class="index-loading" v-if='loading'>加载中......</div>
    <div class="index-loading" v-if='error'>加载失败</div>
  </div>
</template>
<style>
  #video{
    position: relative;
  }
  .slide{
    width: 920px;
    margin: 0 auto;
    overflow: hidden;
    margin-bottom: 40px;
    padding-top: 20px;
  }
  .slide-left,.slide-right{
    font-size: 54px;
    position: absolute;
    font-weight: bold;
    color: #000;
    top: 92px;
  }
  .slide-left:hover,.slide-right:hover{
    color: #666;
  }
  .slide-left{
    left: 30px;
  }
  .slide-right{
    right: 30px;
  }
  .slide>ul{
    display: flex;
  }
  .slide>ul>li{
    display: flex;
    flex-direction: column;
    margin-left: 20px;
  }
  .slide>ul>li:nth-child(5n){
    margin-right: 20px;
  }
  .slide>ul>li>a>img{
    width: 158px;
    height: 88px;
    border: 1px solid #ccc;
  }
  .slide>ul>li:hover .video-title{
    text-decoration: underline;
  }
  .video-title{
    font-size: 14px;
    color: #34a;
    width: 160px;
    display: inline-block;
  }
</style>
<script>
  export default{
    data(){
      return{
        video: {},
        currentPage: 1,
        distance: 0,
        transitionTime: 0,
        transitionFlag: true,
        loading: false,
        error: false
      }
    },
    created: function(){
      this.loading = true;
      this.$jsonp('https://3g.163.com/touch/nc/api/video/recommend/Video_Recom/0-10.do')
      .then(res => {
        var video = res.Video_Recom
        var arr = [];
        for(var j = video.length - 1;j > video.length - 6;j--){
          arr.unshift(video[j])
        }
        var arr2 = [];
        for(var i = 0;i < 5;i++){
          arr2.push(video[i])
        }
        video = arr.concat(video.concat(arr2))
        this.video = video
        this.$nextTick(function(){
          this.distance = document.querySelector('.slide').offsetWidth;
        })
        this.loading = false;
      }).catch(err => {
        this.loading = false;
        this.error = true;
      })
    },
    computed: {
      translateX: function(){
        return 'translateX(' + ( - this.distance * this.currentPage) + 'px)'
      },
      transition: function(){
        return 'all ' + this.transitionTime + 's'
      }
    },
    methods: {
      slide: function(num,time){
        if(this.transitionFlag){
          var that = this;
          this.transitionFlag = false;
          if(num < 0){
            this.currentPage--;
          }else if(num > 0){
            this.currentPage++;
          }
          this.transitionTime = time;
          window.setTimeout(function(){
            that.transitionFlag = true;
            if(that.currentPage == that.video.length / 5 - 1){
              that.transitionTime = 0;
              that.currentPage = 1;
            }else if(that.currentPage == 0){
              that.transitionTime = 0;
              that.currentPage = that.video.length / 5 - 2
            }
          },(time * 1000))
        }
      }
    }
  }
</script>