<template>
  <div id='music'>
    <ul class="music-tab">
      <li v-for='category,index in categories'>
        <router-link :to="{path: '/music/' + category.path}" :class="{'music-current-cat' : currentFlags[index].flag}">{{category.name}}</router-link>
      </li>
    </ul>
    <router-view></router-view>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        categories: [
          {path: 'index' , name: '音乐首页'},
          {path: 'search' , name: '搜索'},
          {path: 'rank' , name: '排行榜'},
          {path: 'broadcast' , name: '电台'}
        ],
        currentFlags: []
      }
    },
    methods: {
      changeCurrentPage: function(path){
        for(var i = 0;i < this.categories.length;i++){
          if(path.split('/')[2] == this.categories[i].path){
            for(var j = 0;j < this.currentFlags.length;j++){
              this.currentFlags[j].flag = false;
            }
            this.currentFlags[i].flag = true;
            break;
          }
        }
      }
    },
    created: function(){
      for(var i = 0;i < this.categories.length;i++){
        this.$set(this.currentFlags,i,{flag : false})
      }
      this.changeCurrentPage(this.$route.path)
    },
    watch: {
      $route(to,from){
        this.changeCurrentPage(to.path)
      }
    }
  }
</script>
<style>
  .music-tab{
    display: flex;
    justify-content: center;
    background-color: #37a;
  }
  .music-tab li{
    padding: 10px 20px;
  }
  .music-tab li a{
    padding: 4px 18px;
    color: #fff;
    border-radius: 20px;
  }
  .music-tab .music-current-cat,.music-tab>li>a:hover{
    background-color: rgba(0,0,0,.3);
  }
</style>