<template>
  <div class="score">
    <div class="stars">
      <span class="grey"></span>
      <span class="yellow-outside" :style="{width: score / 10 * 55 + 'px'}">
        <span class="yellow"></span>
      </span>
    </div>
    <span>{{score}}</span>
  </div>
</template>
<style>
  .score{
    display: flex;
    justify-content: center;
  }
  .score>span{
    font-size: 14px;
    color: red;
    display: inline-block;
    margin-left: 5px;
  }
  .stars{
    width: 56px;
    position: relative;
    height: 11px;
    margin-top: 5px;
  }
  .yellow-outside{
    position: absolute;
    height: 100%;
    width: 100%;
    overflow: hidden;
    display: block;
  }
  .grey,.yellow{
    height: 100%;
    width: 55px;
    display: block;
    position: absolute;
    background-image: url('../../assets/imgs/stars.png');
    background-size: 100%;
  }
  .grey{
    background-position: 0 -231px;
  }
</style>
