<template>
  <div>
    <div class="title">
      <router-link :to="{path: '/movie'}">豆瓣电影 Top 5</router-link>
    </div>
    <div class="movies" v-if='movie[0]'>
        <div class="movie" v-for='item,index in movie' :key='index'>
          <!-- vue请求接口提供的图片链接时会出现“403”，加上'https://images.weserv.nl/?url='前缀可解决，此为缓存图片之网站 -->
          <router-link :to="{path: 'movie/movieDetail' , query: {id : item.id}}" class="movie-post" :title='item.title'>
            <img :src="'https://images.weserv.nl/?url=' + item.images.small">
          </router-link>
          <router-link :to="{path: 'movie/movieDetail' , query: {id : item.id}}" class="movie-name" :title='item.title'>{{item.title}}</router-link>
          <div class="score">
            <div class="stars">
              <span class="grey"></span>
              <span class="yellow-outside" :style="{width: item.rating.average / 10 * 55 + 'px'}">
                <span class="yellow"></span>
              </span>
            </div>
            <span>{{item.rating.average}}</span>
          </div>
        </div>
    </div>
    <div class="index-loading" v-if='loading'>加载中......</div>
    <div class="index-loading" v-if='error'>加载失败</div>
  </div>
</template>
<style>
  .movies{
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
  }
  .movie-post{
    background-color: #ccc;
    border: 1px solid #ccc;
  }
  .movie-post img{
    width: 180px;
    height: 240px;
  }
  .movie{
    display: flex;
    flex-direction: column;
  }
  .movie-name{
    display: block;
    text-align: center;
    color: #000;
    margin-top: 5px;
    margin-bottom: 3px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    max-width: 180px;
  }
  .movie-name:hover{
    color: #072;
  }
  .score{
    display: flex;
    justify-content: center;
  }
  .score>span{
    font-size: 14px;
    color: red;
    display: inline-block;
    margin-left: 5px;
  }
  .stars{
    width: 56px;
    position: relative;
    height: 11px;
    margin-top: 5px;
  }
  .yellow-outside{
    position: absolute;
    height: 100%;
    width: 100%;
    overflow: hidden;
    display: block;
  }
  .grey,.yellow{
    height: 100%;
    width: 55px;
    display: block;
    position: absolute;
    background-image: url('/src/assets/imgs/stars.png');
    background-size: 100%;
  }
  .grey{
    background-position: 0 -231px;
  }
</style>
<script>
  export default{
    data(){
      return{
        movie: {},
        loading: false,
        error: false
      }
    },
    created: function(){
      var that = this;
      that.loading = true;
      this.$jsonp('https://douban.uieee.com/v2/movie/top250?start=0&count=5')
      .then(function(res){
        that.movie = res.subjects;
        that.loading = false;
      }).catch(function(err){
        that.error = true;
        that.loading = false;
      })
    }
  }
</script>