<template>
  <div class="container">
    <template v-if='movie.title'>
      <div class="movie-detail-title">
        <p class="movie-detail-name">{{movie.title}}</p>
        <p class="movie-detail-year">({{movie.year}})</p>
      </div>
      <p class="movie-detail-count" v-if="movie.subtype == 'movie'">{{movie.collect_count}}人看过 / {{movie.wish_count}}人想看</p>
      <p class="movie-detail-count" v-else>{{movie.do_count}}人在看 / {{movie.collect_count}}人看过 / {{movie.wish_count}}人想看</p>
      <div class="movie-detail-inf">
        <img :src="'https://images.weserv.nl/?url=' + movie.images.small" class="movie-detail-cover">
        <div class="movie-detail-casts">
          <ul class="movie-detail-director" v-if='movie.directors[0]'>
            <li>导演：</li>
            <li v-for='item,index in movie.directors' :key='index' v-if='index == 0'><router-link :to="{path: 'artistDetail' , query: {id : item.id}}">{{item.name}}</router-link></li>
            <li v-else>&nbsp;,&nbsp;<router-link :to="{path: 'artistDetail' , query: {id : item.id}}">{{item.name}}</router-link></li>
          </ul>
          <ul class="movie-detail-writer" v-if='movie.writers[0]'>
            <li>编剧：</li>
            <li v-for='item,index in movie.writers' :key='index' v-if='index == 0'><router-link :to="{path: 'artistDetail' , query: {id : item.id}}">{{item.name}}</router-link></li>
            <li v-else>&nbsp;,&nbsp;<router-link :to="{path: 'artistDetail' , query: {id : item.id}}">{{item.name}}</router-link></li>
          </ul>
          <ul class="movie-detail-actor" v-if='movie.casts[0]'>
            <li>主演：</li>
            <li v-for='item,index in movie.casts' :key='index' v-if='index == 0'><router-link :to="{path: 'artistDetail' , query: {id : item.id}}">{{item.name}}</router-link></li>
            <li v-else>&nbsp;,&nbsp;<router-link :to="{path: 'artistDetail' , query: {id : item.id}}">{{item.name}}</router-link></li>
          </ul>
          <p class="movie-detail.genres" v-if='movie.genres[0]'>类型：{{arrayTransform(movie.genres)}}</p>
          <p class="movie-detail-country" v-if='movie.countries[0]'>制片国家 / 地区：{{arrayTransform(movie.countries)}}</p>
          <p class="movie-detail-language" v-if='movie.languages[0]'>语言：{{arrayTransform(movie.languages)}}</p>
          <template v-if="movie.subtype == 'movie'">
            <p class="movie-detail-pubdate" v-if='movie.pubdates[0]'>上映日期：{{arrayTransform(movie.pubdates)}}</p>
            <p class="movie-detail-durations" v-if='movie.durations[0]'>片长：{{arrayTransform(movie.durations)}}</p>
          </template>
          <template v-else>
            <p class="movie-detail-pubdate" v-if='movie.pubdates[0]'>首播时间：{{arrayTransform(movie.pubdates)}}</p>
            <p class="movie-detail-durations" v-if='movie.durations[0]'>每集长度：{{arrayTransform(movie.durations)}}</p>
            <p class="movie-detail-season-current" v-if='movie.current_season'>当前季数：{{movie.current_season}}</p>
            <p class="movie-detail-season-episodes" v-if='movie.episodes_count'>集数：{{movie.episodes_count}}</p>
            <p class="movie-detail-season-count" v-if='movie.seasons_count'>总季数：{{movie.seasons_count}}</p>
          </template>
          <p class="movie-detail-aka" v-if='movie.aka[0]'>又名：{{arrayTransform(movie.aka)}}</p>
        </div>
        <div class="movie-detail-stars">
          <p class="movie-stars-caption">豆瓣评分</p>
          <template v-if='movie.rating.sum !== 0'>
            <div class="movie-stars-score">
              <p>{{Math.floor(movie.rating.average) == movie.rating.average ? movie.rating.average + '.0' : movie.rating.average}}</p>
              <div>
                <div class="movie-stars">
                  <span class="grey-star"></span>
                  <span class="yellow-star" :style="{'width' : movie.rating.average / movie.rating.max * 110 + 'px'}"></span>
                </div>
                <a href="#" class="movie-stars-count">{{movie.rating.sum}}人评价</a>
              </div>
            </div>
            <div class="movie-stars-people">
              <div v-for='n in 5' :key = n>
                <span class="stars-num">{{6 - n}}星</span>
                <div class="stars-length"  :style="{width : movie.rating.details[6 - n] / movie.rating.sum * 100 + 'px'}"></div>
                <span class="stars-percent">{{parseInt(movie.rating.details[6 - n] / movie.rating.sum * 100 * 10) / 10}}%</span>
              </div>
            </div>
          </template>
          <p class="movie-score-no" v-else>暂无评分</p>
        </div>
      </div>
      <div class="movie-summary">
        <p class="movie-captions">{{movie.title}}&nbsp;简介</p>
        <div class="movie-summary-content">
          <p :class="{'movie-describe' : true , 'movie-summary-more' : showMore}" id="summary" v-if='movie.summary' v-html='movie.summary'></p>
          <p class="movie-describe" v-else>暂无简介</p>
          <div class="movie-more" v-if='showMore'>
            <a href="#" @click.prevent='showMore = false'>查看更多</a>
          </div>
        </div>
      </div>
      <div class="movie-photos" v-if="movie.photos[0]">
        <p class="movie-captions">
          电影剧照
        </p>
        <div class="movie-photos-list">
          <a href="#" v-for='item,index in movie.photos' :key='index' class="movie-photos-icon" :style="{'background-image' : 'url(https://images.weserv.nl/?url=' + item.cover + ')'}" @click.prevent='showSlide(true,index)'></a>
        </div>
      </div>
      <div class="movie-comments" v-if='movie.popular_comments[0]'>
        <p class="movie-captions">
          精选评论
        </p>
        <ul>
          <li v-for='item,index in movie.popular_comments' :key='index'>
            <div class="movie-comment-top">
              <div class="movie-comment-author">
                <a href="#">{{item.author.name}}</a>
                <div class="movie-stars">
                  <span class="grey-star"></span>
                  <span class="yellow-star" :style="{width: item.rating.value / item.rating.max * 110 + 'px'}"></span>
                </div>
              </div>
              <p class="movie-comments-word">
                {{item.content}}
              </p>
            </div>
            <div class="movie-comment-bottom">
              <p class="movie-comments-time">{{timeTransform(item.created_at)}}</p>
              <div class="movie-comments-useful">
                {{item.useful_count}}
                <a href="#">有用</a>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="movie-comments" v-if='movie.popular_reviews[0]'>
        <p class="movie-captions">
          精彩影评
        </p>
        <ul>
          <li v-for='item,index in movie.popular_reviews' :key='index'>
            <div class="movie-comment-author">
              <a href="#">{{item.author.name}}</a>
              <div class="movie-stars">
                <span class="grey-star"></span>
                <span class="yellow-star" :style="{width: item.rating.value / item.rating.max * 110 + 'px'}"></span>
              </div>
            </div>
            <a href="#" class="movie-comments-title">{{item.title}}</a>
            <p class="movie-comments-word">
              {{item.summary}}
            </p>
          </li>
        </ul>
      </div>
    </template>
    <p v-else class="movie-detail-loading">加载中......</p>
    <slide :photos='movie.photos' v-if='movie.photos' ref='slide' v-show='slide' @showSlide='showSlide'></slide>
  </div>
</template>
<style>
  .movie-captions{
    margin-top: 10px;
    font-size: 28px;
    margin-bottom: 8px;
  }
  .movie-detail-title{
    display: flex;
    font-size: 30px;
    margin-top: 20px;
    margin-bottom: 10px;
  }
  .movie-detail-count{
    font-size: 18px;
    margin-bottom: 20px;
  }
  .movie-detail-inf{
    display: flex;
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;
    align-items: flex-start;
  }
  .movie-detail-cover{
    width: 152px;
    height: 215px;
    border: 1px solid #ccc;
  }
  .movie-detail-casts{
    width: 420px;
    margin-left: 20px;
  }
  .movie-detail-casts ul{
    display: flex;
    flex-wrap: wrap;
  }
  .movie-detail-casts ul li,.movie-detail-casts ul li a{
    color: #000;
  }
  .movie-detail-casts ul li a:hover{
    text-decoration: underline;
  }
  .movie-detail-stars{
    margin-left: 20px;
    border-left: 1px solid #ccc;
    padding-left: 10px;
  }
  .movie-stars-score{
    display: flex;
    margin-bottom: 10px;
    margin-top: 8px;
  }
  .movie-stars-score>p,.movie-score-no{
    font-size: 30px;
    margin-right: 15px;
  }
  .movie-stars{
    position: relative;
    width: 110px;
    padding-top: 22px;
    margin-bottom: 2px;
  }
  .movie-stars-count{
    font-size: 14px;
    color: #000;
  }
  .movie-stars-count:hover{
    text-decoration: underline;
  }
  .movie-stars span{
    display: inline-block;
    height: 22px;
    /*background-image: url(/src/assets/imgs/stars.png);*/
    background-image: url(../../assets/imgs/stars.png);
    position: absolute;
    top: 0px;
    left: 0px;
  }
  .grey-star{
    background-position: 0 -220px;
    width: 100%;
  }
  .yellow-star{
    background-position: 0 0;
  }
  .movie-stars-people>div{
    display: flex;
    font-size: 14px;
    margin-bottom: 7px;
  }
  .stars-length{
    width: 100px;
    background-color: #ffd596;
    margin-left: 7px;
    margin-right: 7px;
  }
  .movie-summary{
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;
  }
  .movie-summary-content{
    position: relative;
  }
  .movie-summary-more{
    height: 150px;
    overflow: hidden;
  }
  .movie-more{
    position: absolute;
    text-align: center;
    width: 100%;
    bottom: 0;
    padding: 11px 0;
    background-color: rgba(155,155,155,.9);
  }
  .movie-more a{
    color: #000;
  }
  .movie-more a:hover{
    color: #072;
  }
  .movie-photos{
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;
  }
  .movie-photos-list{
    display: flex;
    flex-wrap: wrap;
  }
  .movie-photos-icon{
    width: 135px;
    height: 112px;
    margin-right: 20px;
    margin-top: 20px;
    display: block;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border: 1px solid #ccc;
  }
  .movie-comments ul{
    margin-top: 10px;
  }
  .movie-comments ul li{
    border-bottom: 1px solid #ccc;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .movie-comment-author{
    display: flex;
  }
  .movie-comment-author>a{
    display: inline-block;
    margin-right: 6px;
  }
  .movie-comments-title{
    display: inline-block;
    margin-top: 10px;
  }
  .movie-comments-word{
    margin-top: 7px;
    margin-bottom: 7px;
  }
  .movie-comment-bottom{
    display: flex;
  }
  .movie-comments-useful{
    margin-left: 20px;
  }
  .movie-comments ul li a{
    color: #37a;
  }
  .movie-comments ul li a:hover{
    color: #fff;
    background-color: #37a;
  }
  .movie-detail-loading{
    font-size: 30px;
    margin-top: 20px;
  }
</style>
<script>
  import slide from './photoSlide.vue'
  export default{
    data(){
      return{
        movie: {},
        showMore: false,
        slide: true
      }
    },
    components: {slide},
    created: function(){
      this.getData(this.$route.query.id);
    },
    watch: {
      $route: function(){
        this.getData(this.$route.query.id);
      }
    },
    methods: {
      arrayTransform: function(fakeArr){
        var arr = [];
        //由于接口(professions)数组中有空数据，用此方法去除之
        for(var i = 0;i < fakeArr.length;i++){
          if(fakeArr[i].trim() !== ''){
            arr.push(fakeArr[i])
          }
        }
        var str = '';
        for(var i = 0;i < arr.length;i++){
          if(i == arr.length - 1){
            str = str + arr[i];
          }else{
            str = str + arr[i] + ' / ';
          }
        }
        return str;
      },
      getData: function(id){
        this.$jsonp('https://douban.uieee.com/v2/movie/subject/' + id)
        .then(res => {
          this.movie = res;
          var sum = 0;
          for(var key in this.movie.rating.details){
            sum = sum + this.movie.rating.details[key]
          }
          this.movie.rating.sum = sum
          this.movie.summary = this.movie.summary.replace(/\n/g,"<br/>")
          this.$nextTick(function(){
            this.slide = false;
            if(document.getElementById('summary').offsetHeight > 200){
              this.showMore = true;
            }else{
              this.showMore = false;
            }
          })
        })
      },
      timeTransform: function(str){
        var thisDate = new Date();
        var date = new Date(str);
        var timeStr = "";
        var thisHour = thisDate.getHours();
        var hour = date.getHours();
        var thisMinute = thisDate.getMinutes();
        var minute = date.getMinutes();
        if(thisHour < 10){
          thisHour = "0" + thisHour;
        }
        if(hour < 10){
          hour = "0" + hour;
        }
        if(thisMinute < 10){
          thisMinute = "0" + thisMinute;
        }
        if(minute < 10){
          minute = "0" + minute;
        }
        var space = (thisDate - date) / 1000;
        if(space < 60){
          if(thisDate.getSeconds() > date.getSeconds()){
            return timeStr = (thisDate.getSeconds() - date.getSeconds()) + "秒前";
          }else if(thisDate.getSeconds() < date.getSeconds()){
            return timeStr = (thisDate.getSeconds() + 60 - date.getSeconds()) + "秒前";
          }else{
            return timeStr = "1秒前";
          }
        }

        if(space / 3600 < 1){
          if(thisDate.getMinutes() > date.getMinutes()){
            return timeStr = (thisDate.getMinutes() - date.getMinutes()) + "分钟前";
          }else{
            return timeStr = (thisDate.getMinutes() + 60 - date.getMinutes()) + "分钟前";
          }
        }

        if(space / 3600 / 24 < 1 && thisDate.getDay() == date.getDay()){
          return timeStr = hour + ":" + minute; 
        }
        if(space / 3600 / 24 < 2 && (thisDate.getDay() - date.getDay() == 1 || thisDate.getDay() - date.getDay() == -6)){
          return timeStr = "昨天" + hour + ":" + minute;
        }
        if((space / 3600 / 24 > 2 && thisDate.getFullYear() == date.getFullYear()) || ((space / 3600 / 24 > 1 && thisDate.getDay() - date.getDay() !== 1 && thisDate.getDay() - date.getDay() !== -6) && thisDate.getFullYear() == date.getFullYear())){
          return timeStr = (date.getMonth() + 1) + "月" + date.getDate() + "日 " + hour + ":" + minute;
        }
        return timeStr = date.getFullYear() + "年" + (date.getMonth() + 1) + "月" + date.getDate() + "日";
        
        // 一分钟内
        // 一小时内
        // 今天
        // 昨天
        // 两天前 - 年头
        // 去年
      },
      showSlide: function(show,index){
        this.slide = show;
        if(index !== undefined){
          this.$refs.slide.selectPhoto(index)
        }
      }
    }
  }
</script>