<template>
  <div class="music-index-search">
    <label>
      <input type="text" class="music-index-search-ipt" placeholder="输入歌名/歌手" v-model='input'>
      <a href="#" class="music-index-search-btn" @click.prevent='search'>搜索</a>
    </label>
  </div>
</template>
<style>
  .music-index-search{
    display: flex;
    justify-content: center;
  }
  .music-index-search label{
    margin: 30px 0;
  }
  .music-index-search-ipt{
    width: 400px;
    height: 40px;
    border: 0;
    outline: none;
    font-size: 18px;
    font-family: "微软雅黑";
    padding-left: 24px;
    border-radius: 30px;
    background-color: #eee;
    margin-right: 10px;
  }
  .music-index-search-btn{
    color: #000;
  }
  .music-index-search-btn:hover{
    color: red;
  }
</style>
<script>
  export default{
    data(){
      return{
        input: ''
      }
    },
    methods: {
      search: function(){
        this.$router.push({name: 'musicSearch' , params: {name : this.input}})
        this.$emit('search',this.input)
      }
    },
    props: ['keyword'],
    mounted: function(){
      if(this.keyword !== ""){
        this.input = this.keyword
      }
      var that = this;
      var input = document.querySelector('.music-index-search-ipt');
      input.onkeyup = function(e){
        if(e.keyCode == 13){
          if(that.input.trim() !== ""){
            that.search()
          }
          input.blur();
        }
      }
    }
  }
</script>