<template>
  <div class="container">
    <video :src='src' controls="controls" class="video"></video>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        src: '',
        list: []
      }
    },
    mounted: function(){
      this.src = this.$route.query.src;
      document.querySelector('.video').load();
    }
  }
</script>