<template>
  <div class="container">
    <div class="news-cats">
      <a href="#" @click.prevent='getData(index)' v-for='item,index in newsCats' :key='index' :class="{'current-cat' : newsCats[index].flag}">{{item.name}}</a>
    </div>
    <div class="news-list" v-show='showList'>
      <a href="#" class="news-list-item" v-for='news,idx in list' :key='idx'>
        <img :src="news.imgsrc">
        <div class="news-inf">
          <p class="news-desc">{{news.title}}</p>
          <p class="news-digest" v-if='news.digest'>{{news.digest}}...</p>
          <p class="news-digest" v-else></p>
          <div class="news-inf-bottom">
            <span class="news-source" v-if='news.source'>{{news.source}}</span>
            <span class="news-time">{{timeTransform(news.ptime)}}</span>
          </div>
        </div>
      </a>
    </div>
    <p class="news-loading" v-show='!showList'>加载中。。。</p>
  </div>
</template>
<style>
  .news-cats{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: #ccc;
    border-radius: 20px;
  }
  .news-cats a{
    display: block;
    text-align: center;
    width: 100%;
    padding: 10px;
    color: #000;
  }
  .news-cats .current-cat{
    color: red;
  }
  .news-cats a:hover{
    color: red;
  }
  .news-list{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .news-list-item{
    display: flex;
    width: 353px;
    box-sizing: border-box;
    padding: 5px;
    margin-bottom: 10px;
  }
  .news-list-item:hover{
    background-color: rgba(0,0,0,.1);
  }
  .news-list-item>img{
    width: 120px;
    height: 90px;
  }
  .news-inf{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 213px;
    margin-left: 10px;
  }
  .news-desc{
    font-size: 14px;
    color: #37a;
  }
  .news-digest{
    font-size: 12px;
    color: #ccc;
  }
  .news-inf-bottom{
    font-size: 12px;
    display: flex;
  }
  .news-source{
    color: #666;
    max-width: 120px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: block;
    margin-right: 3px;
  }
  .news-time{
    color: #666;
  }
  .news-loading{
    font-size: 18px;
    margin-top: 20px;
    text-align: center;
  }
</style>
<script>
  export default{
    data(){
      return{
        list: [],
        //显示条目数
        items: 12,
        //第一次加载时默认的栏目的index
        defaultIndex: 0,
        //最小刷新间隔，以分钟为单位，在此时间内多次点击同一栏目按钮只会发一次请求
        spaceTime: 20,
        //缓存存放处
        cache: [],
        showList: true,
        newsCats: [
          {name : '新闻' , path : 'BBM54PGAwangning' , flag : false},
          {name : '娱乐' , path : 'BA10TA81wangning' , flag : false},
          {name : '体育' , path : 'BA8E6OEOwangning' , flag : false},
          {name : '财经' , path : 'BA8EE5GMwangning' , flag : false},
          {name : '军事' , path : 'BAI67OGGwangning' , flag : false},
          {name : '科技' , path : 'BA8D4A3Rwangning' , flag : false},
          {name : '手机' , path : 'BAI6I0O5wangning' , flag : false},
          {name : '数码' , path : 'BAI6JOD9wangning' , flag : false},
          {name : '时尚' , path : 'BA8F6ICNwangning' , flag : false},
          {name : '游戏' , path : 'BAI6RHDKwangning' , flag : false},
          {name : '教育' , path : 'BA8FF5PRwangning' , flag : false},
          {name : '健康' , path : 'BDC4QSV3wangning' , flag : false},
          {name : '旅游' , path : 'BEO4GINLwangning' , flag : false},
        ]
      }
    },
    methods: {
      timeTransform: function(str){
        var thisDate = new Date();
        var date = new Date(str);
        var timeStr = "";
        var thisHour = thisDate.getHours();
        var hour = date.getHours();
        var thisMinute = thisDate.getMinutes();
        var minute = date.getMinutes();
        if(thisHour < 10){
          thisHour = "0" + thisHour;
        }
        if(hour < 10){
          hour = "0" + hour;
        }
        if(thisMinute < 10){
          thisMinute = "0" + thisMinute;
        }
        if(minute < 10){
          minute = "0" + minute;
        }
        var space = (thisDate - date) / 1000;
        if(space < 60){
          if(thisDate.getSeconds() > date.getSeconds()){
            return timeStr = (thisDate.getSeconds() - date.getSeconds()) + "秒前";
          }else if(thisDate.getSeconds() < date.getSeconds()){
            return timeStr = (thisDate.getSeconds() + 60 - date.getSeconds()) + "秒前";
          }else{
            return timeStr = "1秒前";
          }
        }

        if(space / 3600 < 1){
          if(thisDate.getMinutes() > date.getMinutes()){
            return timeStr = (thisDate.getMinutes() - date.getMinutes()) + "分钟前";
          }else{
            return timeStr = (thisDate.getMinutes() + 60 - date.getMinutes()) + "分钟前";
          }
        }

        if(space / 3600 / 24 < 1 && thisDate.getDay() == date.getDay()){
          return timeStr = hour + ":" + minute; 
        }
        if(space / 3600 / 24 < 2 && (thisDate.getDay() - date.getDay() == 1 || thisDate.getDay() - date.getDay() == -6)){
          return timeStr = "昨天" + hour + ":" + minute;
        }
        if((space / 3600 / 24 > 2 && thisDate.getFullYear() == date.getFullYear()) || ((space / 3600 / 24 > 1 && thisDate.getDay() - date.getDay() !== 1 && thisDate.getDay() - date.getDay() !== -6) && thisDate.getFullYear() == date.getFullYear())){
          return timeStr = (date.getMonth() + 1) + "月" + date.getDate() + "日 " + hour + ":" + minute;
        }
        return timeStr = date.getFullYear() + "年" + (date.getMonth() + 1) + "月" + date.getDate() + "日";
        
        // 一分钟内
        // 一小时内
        // 今天
        // 昨天
        // 两天前 - 年头
        // 去年
      },
      getData: function(index){
        if(!this.showList) return;
        this.$router.push({path: '/news' , query: {index: index}})
        for(var i = 0;i < this.newsCats.length;i++){
          if(i == index){
            this.newsCats[i].flag = true;
          }else{
            this.newsCats[i].flag = false;
          }
        }
        var time = Date.parse(new Date());
        var cacheIndex = false;
        for(var i = 0;i < this.cache.length;i++){
          var item = this.cache[i]
          if(item.index == index){
            //接口问题，若第一次没拿到数据，则该验证直接跳出
            if(!item.list){
              break;
            }
            if(time - item.time < this.spaceTime * 60 * 1000){
              this.list = item.list;
              return;
            }
            cacheIndex = i;
            break;
          }
        }
        var path = this.newsCats[index].path;
        var url = 'https://3g.163.com/touch/reconstruct/article/list/' + path + '/0-' + this.items + '.html';
        this.showList = false;
        this.$jsonp(url,{callbackName: 'artiList'})
        .then(res => {
          this.list = res[path]
          var obj = {}
          obj.index = index;
          obj.list = res[path];
          obj.time = Date.parse(new Date());
          if(cacheIndex){
            this.cache[cacheIndex] = obj;
          }else{
            this.cache.push(obj)
          }
          this.showList = true;
        })
      }
    },
    created: function(){
      if(this.$route.query.index){
        this.getData(parseInt(this.$route.query.index))
      }else{
        this.getData(this.defaultIndex)
      }
    }
  }
</script>