<template>
  <div class="container broadcast-detail">
    <div class="broadcast-inf">
      <img :src="src">
      <p class="broadcast-channel">{{channel}} : {{name}}</p>
    </div>
    <music-list :songs='list' v-if='showList'></music-list>
  </div>
</template>
<style>
  .broadcast-detail{
    margin-top: 40px;
  }
  .broadcast-inf{
    display: flex;
    width: 700px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  .broadcast-inf>img{
    width: 150px;
    height: 150px;
    border: 1px solid #ccc;
  }
  .broadcast-channel{
    font-size: 30px;
    margin-left: 100px;
    padding-top: 15px;
  }
</style>
<script>
  export default{
    data(){
      return{
        list: {
          list: [],
          rank: false,
          album: false
        },
        channel: "",
        name: "",
        src: "",
        showList: false
      }
    },
    created: function(){
      var params = this.$route.params
      this.src = params.src;
      this.channel = params.channel;
      this.name = params.name;
      if(params.channel == "音乐人频道"){
        this.$axios.get('https://api.apiopen.top/searchMusic?name=' + params.name)
        .then(res => {
          var data = res.data.result;
          var arr = []
          for(var i = 0;i < data.length;i++){
            var obj = {};
            obj.author = data[i].author;
            obj.title = data[i].title;
            arr.push(obj)
          }
          this.list.list = arr;
          this.showList = true;
        })
      }else{
        this.$axios.get('https://api.apiopen.top/musicBroadcastingDetails?channelname=' + params.ch_name)
        .then(res => {
          var arr = []
          var data = res.data.result.songlist
          for(var i = 0;i < data.length;i++){
            var obj = {};
            obj.author = data[i].artist;
            obj.title = data[i].title;
            arr.push(obj)
          }
          this.list.list = arr;
          this.showList = true;
          console.log(2)
        })
      }
      
    }
  }
</script>