<template>
  <div class="container">
    <div class="channel-selection">
      <span>频道选择：</span>
      <select class="channel-selector">
        <option v-for='broadcast,index in broadcasts' :key='index'>{{broadcast.name}}</option>
      </select>
    </div>
    <div class="music-broadcast" v-show='broadcasts'>
      <p>{{currentChannel.name}}</p>
      <div class="music-broadcast-channel">
        <router-link :to="{name: 'BroadcastDetail', params: {src: item.thumb?item.thumb:item.avatar , channel: item.cate_sname?item.cate_sname:'音乐人频道' , ch_name: item.ch_name?item.ch_name:item.name , name: item.name}}" v-for='item,idx in currentChannel.list' :key='idx' :title='item.name'>
          <img v-if='item.thumb' :src="item.thumb">
          <img v-else :src="item.avatar">
          <p class="music-broadcast-name">{{item.name}}</p>
        </router-link>
      </div>
    </div>
    <div class="index-loading" v-show='loading'>加载中......</div>
    <div class="index-loading" v-show='error'>加载失败</div>
  </div>
</template>
<style>
  .channel-selection{
    margin-top: 20px;
  }
  .channel-selector{
    padding: 5px 5px;
    font-size: 18px;
    font-family: "微软雅黑";
    border: 0;
    background: transparent;
    margin-bottom: 20px;
  }
  .music-broadcast{
    margin-left: 10px;
    margin-bottom: 20px;
  }
  .music-broadcast>p{
    font-size: 18px;
    margin-bottom: 5px;
    border-left: 5px solid #072;
    padding-left: 6px;
  }
  .music-broadcast-channel{
    display: flex;
    flex-wrap: wrap;
  }
  .music-broadcast-channel>a{
    display: block;
    width: 110px;
    margin-left: 10px;
    padding-bottom: 10px;
  }
  .music-broadcast-channel>a:hover .music-broadcast-name{
    color: #072;
  }
  .music-broadcast-channel>a>img{
    height: 90px;
    width: 90px;
    margin-top: 10px;
    margin-left: 10px;
    border-radius: 50%;
  }
  .music-broadcast-name{
    text-align: center;
    color: #000;
  }
</style>
<script>
  export default{
    data(){
      return{
        broadcasts: [],
        currentChannel: {},
        loading: false,
        error: false
      }
    },
    methods: {
      selectChannel: function(index){
        this.currentChannel = this.broadcasts[index]
        console.log(this.currentChannel)
      }
    },
    created: function(){
      this.loading = true;
      this.$axios.get('https://api.apiopen.top/musicBroadcasting')
      .then(res => {
        var data = res.data.result[0].channellist;
        var arr = [];
        var index = 0;
        var findAttr = function(attr){
          var flag = false;
          for(var j = 0;j < arr.length;j++){
            if(arr[j].name == attr){
              flag = j;
              break;
            }
          }
          return flag;
        }
        for(var i = 0;i < data.length;i++){
          if(findAttr(data[i].cate_sname) === false){
            arr[index] = {};
            arr[index].name = data[i].cate_sname;
            arr[index].list = [];
            arr[index].list.push(data[i])
            index++;
          }else{
            arr[findAttr(data[i].cate_sname)].list.push(data[i])
          }
        }
        var star = {};
        var starData = res.data.result[1]
        star.name = starData.title;
        star.list = starData.channellist;
        arr.push(star)
        this.broadcasts = arr;
        this.selectChannel(0)
        this.loading = false;
      }).catch(err => {
        this.error = true;
        this.loading = false;
      })
    },
    mounted: function(){
      var that = this;
      var selector = document.querySelector('.channel-selector')
      selector.onchange = function(){
        for(var i = 0;i < selector.children.length;i++){
          if(selector.children[i].selected == true){
            that.selectChannel(i)
            break;
          }
        }
      }
    }
  }
</script>