<template>
  <div id="app">
    <div class="topbar-bgc">
      <div class="topbar">
        <router-link :to="{path: '/'}" class="logo"><img src="./assets/imgs/logo_db.png"></router-link>
        <ul class="topbar-cat">
          <li v-for='cat,index in cats'><router-link :to="{path: cat.path}" :class='{"topbar-current": currentFlags[index].flag }'>{{cat.name}}</router-link></li>
        </ul>
      </div>
    </div>
    <router-view></router-view>
    <div class="bottom">
      <p>© 2005－2019 douban.com, all rights reserved 北京豆网科技有限公司</p>
      <ul>
        <li><a href="#">关于豆瓣</a></li>
