        <li><a href="#">联系我们</a></li>
        <li><a href="#">法律声明</a></li>
        <li><a href="#">帮助中心</a></li>
        <li><a href="#">移动应用</a></li>
        <li><a href="#">豆瓣广告</a></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      cats: [
        {path: "/index" , name: "首页"},
        {path: "/book" , name: "小说"},
        {path: "/movie" , name: "电影"},
        {path: "/music" , name: "音乐"},
        {path: "/video" , name: "视频"},
        {path: "/news" , name: "新闻"}
      ],
      currentFlags: []
    }
  },
  methods: {
    changeCurrentPage: function(path){
      for(var i = 0;i < this.cats.length;i++){
        if(path.split('/')[1] == this.cats[i].path.split('/')[1]){
          for(var j = 0;j < this.currentFlags.length;j++){
            this.currentFlags[j].flag = false;
          }
          this.currentFlags[i].flag = true;
          break;
        }
      }
    }
  },
  created: function(){
    for(var i = 0;i < this.cats.length;i++){
      this.$set(this.currentFlags,i,{flag : false})
    }
    this.changeCurrentPage(this.$route.path)
  },
  watch: {
    $route(to,from){
      this.changeCurrentPage(to.path)
    }
  }
}
</script>
