<template>
  <div id="news">
    <div class="title">
      <router-link :to="{path: '/news'}">豆瓣新闻</router-link>
    </div>
    <div class="slide" v-if='news[0]'>
      <a href="#" class="slide-left" @click.prevent="slide(-1,1)"><i class="icofont-simple-left"></i></a>
      <a href="#" class="slide-right" @click.prevent="slide(1,1)"><i class="icofont-simple-right"></i></a>
      <ul id="slide_area1" :style="{transform : translateX,transition : transition}">
        <li v-for='item,index in news' :key='index' :title='item.title'>
          <a href="#"><img :src="item.imgsrc"></a>
          <a href="#" class="news-title">{{item.title}}</a>
        </li>
      </ul>
    </div>
    <div class="index-loading" v-if='loading'>加载中......</div>
    <div class="index-loading" v-if='error'>加载失败</div>
  </div>
</template>
<style>
  #news{
    position: relative;
  }
  #news .slide{
    width: 920px;
    margin: 0 auto;
    overflow: hidden;
    margin-bottom: 40px;
    padding-top: 20px;
  }
  #news .slide-left,#news .slide-right{
    font-size: 54px;
    position: absolute;
    font-weight: bold;
    color: #000;
    top: 92px;
  }
  #news .slide-left:hover,#news .slide-right:hover{
    color: #666;
  }
  #news .slide-left{
    left: 30px;
  }
  #news .slide-right{
    right: 30px;
  }
  #news .slide>ul{
    display: flex;
  }
  #news .slide>ul>li{
    display: flex;
    flex-direction: column;
    margin-left: 20px;
  }
  #news .slide>ul>li:nth-child(5n){
    margin-right: 20px;
  }
  #news .slide>ul>li>a>img{
    width: 158px;
    height: 88px;
    border: 1px solid #ccc;
  }
  #news .slide>ul>li:hover .news-title{
    text-decoration: underline;
  }
  #news .news-title{
    font-size: 14px;
    color: #34a;
    width: 160px;
    display: inline-block;
  }
</style>
<script>
  export default{
    data(){
      return{
        news: {},
        currentPage: 1,
        distance: 0,
        transitionTime: 0,
        transitionFlag: true,
        loading: false,
        error: false
      }
    },
    created: function(){
      var that = this;
      that.loading = true;
      this.$jsonp('https://3g.163.com/touch/reconstruct/article/list/BBM54PGAwangning/0-10.html',{
        callbackName: 'artiList'
      })
      // 该接口需要提供callbackName,不能在url后加callback=xxxx
      .then(res => {
        var news = res.BBM54PGAwangning
        var arr = [];
        for(var j = news.length - 1;j > news.length - 6;j--){
          arr.unshift(news[j])
        }
        var arr2 = [];
        for(var i = 0;i < 5;i++){
          arr2.push(news[i])
        }
        news = arr.concat(news.concat(arr2))
        this.news = news
        this.$nextTick(function(){
          this.distance = document.querySelector('.slide').offsetWidth;
        })
        that.loading = false;
      }).catch(function(err){
        console.log(err)
        that.loading = false;
        that.error = true;
      })
    },
    computed: {
      translateX: function(){
        return 'translateX(' + ( - this.distance * this.currentPage) + 'px)';
      },
      transition: function(){
        return 'all ' + this.transitionTime + 's';
      }
    },
    methods: {
      slide: function(num,time){
        if(this.transitionFlag){
          var that = this;
          this.transitionFlag = false;
          if(num < 0){
            this.currentPage--;
          }else if(num > 0){
            this.currentPage++;
          }
          this.transitionTime = time;
          window.setTimeout(function(){
            that.transitionFlag = true;
            if(that.currentPage == that.news.length / 5 - 1){
              that.transitionTime = 0;
              that.currentPage = 1;
            }else if(that.currentPage == 0){
              that.transitionTime = 0;
              that.currentPage = that.news.length / 5 - 2
            }
          },(time * 1000))
        }
      }
    }
  }
</script>