<template>
  <div>
    <div class="novel-detail-outside" v-show='detail.title'>
      <p class="novel-caption">小说详情</p>
      <div class="novel-detail">
        <img class="novel-cover" :src="detail.cover">
        <div class="novel-inf">
          <p class="novel-title">书名：{{detail.title}}</p>
          <p class="novel-author">作者：{{detail.author}}</p>
          <p class="novel-category">分类：{{detail.category}}</p>
          <p class="novel-tags">标签：{{detail.tags}}</p>
          <p class="novel-words">字数：{{detail.words}}字</p>
          <p class="novel-desc">摘要：{{detail.desc}}</p>
          <p class="novel-first" v-if='detail.first_chapter'>开始章节：{{detail.first_chapter.cname}}</p>
          <p class="novel-latest" v-if='detail.first_chapter'>最后更新：{{detail.latest_chapter.cname}}</p>
        </div>
      </div>
      <p class="novel-caption">为您推荐</p>
      <ul class="novel-recommend">
        <li v-for='item,index in recommend' :key='index'>
          <div class="novel-rec-inf">
            <a href="#" class="novel-rec-cover" @click.prevent='getData(item.title)'><img :src="item.cover"></a>
            <a href="#" class="novel-rec-title" :title="item.title" @click.prevent='getData(item.title)'>{{item.title}}</a>
            <span class="novel-rec-author" :title="item.author">{{item.author}}</span>
          </div>
          <div class="novel-rec-des">
            <p class="novel-rec-words">字数：{{item.words}}字</p>
            <p class="novel-rec-category">类别：{{item.category}}</p>
            <p class="novel-rec-tags">标签：{{item.tags}}</p>
            <p class="novel-rec-desc">摘要：{{item.desc}}</p>
            <p class="novel-rec-first">开始章节：{{item.first_chapter}}</p>
          </div>
        </li>
      </ul>
    </div>
    <div class="index-loading" v-show='!detail.title&&!keyIsAuthor'>加载中......</div>
    <div class="warning" v-if='keyIsAuthor'>目前尚未支持作者名搜索，请返回上一页再试</div>
  </div>
</template>
<style>
  .novel-detail-outside{
    width: 1100px;
    margin: 0 auto;
  }
  .novel-caption{
    font-size: 36px;
    margin-top: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
  }
  .novel-detail{
    margin-top: 15px;
    display: flex;
    padding: 0 15px;
    justify-content: space-between;
  }
  .novel-cover{
    width: 210px;
    height: 280px;
  }
  .novel-inf{
    width: 840px;
  }
  .novel-inf p{
    color: #666;
    margin-bottom: 3px;
  }
  .novel-recommend li{
    display: flex;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
    margin-top: 15px;
  }
  .novel-rec-time{
    width: 100%;
    padding-bottom: 10px;
  }
  .novel-rec-result{
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid #ccc;
  }
  .novel-rec-result>li{
    display: flex;
    padding-top: 20px;
    padding-bottom: 10px;
    border-top: 1px solid #ccc;
    padding-left: 20px;
    padding-right: 20px;
  }
  .novel-rec-inf{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
  }
  .novel-rec-cover img{
    width: 150px;
    height: 200px;
    border: 1px solid #ccc;
  }
  .novel-rec-title{
    color: #37a;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .novel-rec-des{
    color: #666;
    margin-top: 7px;
    font-size: 14px;
  }
  .novel-rec-des p{
    margin-bottom: 9px;
  }
  .novel-rec-title,.novel-rec-author{
    max-width: 150px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .warning{
    text-align: center;
    font-size: 30px;
  }
</style>
<script>
  export default{
    data(){
      return{
        detail: {},
        recommend: {},
        dataLost: false,
        keyIsAuthor: false
      }
    },
    methods: {
      getData: function(name){
        this.$router.push({path: 'detail' , query: {name:name}})
        this.detail = {};
        this.$axios.get('https://www.apiopen.top/novelInfoApi?name=' + name)
        .then(res => {
          this.dataLost = true;
          this.detail = res.data.data.aladdin;
          this.recommend = res.data.data.data;
          if(!this.detail.title&&this.detail.author){
            this.keyIsAuthor = true;
          }
        })
        document.documentElement.scrollTop = document.body.scrollTop = 0;
      }
    },
    created: function(){
      this.getData(this.$route.query.name);
    }
  }
</script>