<template>
  <div>
    <div class="title">
      <router-link :to="{path: '/music'}">豆瓣音乐</router-link>
    </div>
    <div class="music" v-if='charts[0]'>
      <div class="chart" v-for='chart,index in charts' :key='index'>
        <div class="music-top">
          <span class="chart-name">{{chart.name}}</span>
          <router-link class='more' :to="{name: 'musicRank' , params: {index : index}}">查看更多</router-link>
        </div>
        <div class="songs">
          <div class="song" v-for='song,idx in chart.content' :key='idx'>
            <a href="#" :title='song.title'><img :src="song.pic_big"></a>
            <a href="#" class="song-name" :title='song.title'>{{song.title}}</a>
            <router-link  class="song-artist" :to="{name: 'musicSearch' , params: {name : song.author}}" :title='song.author'>{{song.author}}</router-link>
          </div>
        </div>
      </div>
    </div>
    <div class="index-loading" v-if='loading'>加载中......</div>
    <div class="index-loading" v-if='error'>加载失败</div>
  </div>
</template>
<style>
  .music{
    padding: 0 60px;
  }
  .chart{
    margin-bottom: 24px;
  }
  .music-top{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
    margin-bottom: 15px;
  }
  .chart-name{
    font-size: 22px;
    display: inline-block;
  }
  .more{
    font-size: 14px;
    color: #000;
  }
  .more:hover{
    color: #072;
  }
  .songs{
    display: flex;
    width: 100%;
  }
  .songs>.song:nth-child(n+2){
    margin-left: 40px;
  }
  .song{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .song img{
    width: 150px;
    height: 150px;
    border: 1px solid #ccc;
  }
  .song-name{
    color: #3377aa;
    margin-top: 8px;
    margin-bottom: 2px;
  }
  .song-artist{
    color: #000;
    font-size: 14px;
  }
  .song-name,.song-artist{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    max-width: 150px;
  }
  .song-name:hover,.song-artist:hover{
    text-decoration: underline;
  }
</style>
<script>
  export default{
    data(){
      return{
        charts: {},
        loading: false,
        error: false
      }
    },
    created: function(){
      this.loading = true;
      var that = this;
      this.$axios.get('https://api.apiopen.top/musicRankings')
      .then(function(res){
        that.charts = res.data.result;
        that.loading = false;
      }).catch(function(err){
        that.error = true;
        that.loading = false;
      })
    }
  }
</script>